import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Comprobante, Concepto, Configuracion, ReciboServicio, Bien, ConceptoComprobante } from 'src/app/entities/business-control';
import { BusinessControlService } from 'src/app/services/business-control/business-control.service';
import { Chart } from 'chart.js';
import { PdfService } from 'src/app/services/pdf/pdf.service';
import { ToastrService } from 'ngx-toastr';
import { GoogleService, UserGoogle } from 'src/app/services/google-services/google.service';
import { CryptService } from 'src/app/services/crypt/crypt.service';

@Component({
  selector: 'app-business-control',
  templateUrl: './business-control.component.html',
  styleUrls: ['./business-control.component.css']
})
export class BusinessControlComponent implements OnInit {
  comprobanteForm: FormGroup;
  selectedBien: Bien | null = null;
  selectedBienId: string;
  config: Configuracion | null = null;
  servicioForm: FormGroup;
  newComprobante: Comprobante = null;
  chart: any;
  observacionComprobante: string = '';
  metodoPagoComprobante: string = '';
  fechaComprobante: string = new Date().toISOString().substring(0, 10);

  @ViewChild('pdfContent', { static: false }) pdfContent: ElementRef;
  comprobanteToPdf: Comprobante | null = null;
  firmaDigital: string = '';
  isWebShareSupported: boolean = false;
  conceptosByContrato: Concepto[] = [];
  userLogger: UserGoogle;

  constructor(private googleService: GoogleService, private cryptService: CryptService, private toastr: ToastrService, private fb: FormBuilder, private businessService: BusinessControlService, private router: Router, private pdfService: PdfService, private route: ActivatedRoute) {
    this.comprobanteForm = this.fb.group({
      descripcion: ['', Validators.required],
      descripcionTemp: [''],
      valor: ['', Validators.required],
      submitted: [false]
    });
    this.servicioForm = this.fb.group({
      tipo: ['', Validators.required],
      valor: ['', Validators.required],
      fechaGeneracion: ['', Validators.required],
      fechaPago: ['', Validators.required],
      submitted: [false]
    });
    this.isWebShareSupported = !!navigator.share;
  }

  private InicializarComprobante() {
    console.log("inicializando")
    if (!this.newComprobante || (this.newComprobante && !this.newComprobante.id)) {
      this.observacionComprobante = "";
      this.newComprobante = {
        id: '',
        conceptos: [],
        total: 0,
        fechaGeneracion: this.businessService.instanceDateFromString(this.fechaComprobante),
        periodo: {
          inicio: new Date(),
          fin: new Date(),
        },
        observacion: '',
        contratoId: '',
        medioPago: 'Efectivo',
      };
    }
  }

  ngOnInit(): void {
    // Obtener el valor del query param "comprobante"
    this.route.queryParamMap.subscribe(params => {
      try {
        const comprobanteCrypted = params.get('comprobante');
        if (comprobanteCrypted) {
          console.log("asignando")
          const aux = this.cryptService.decrypt(comprobanteCrypted);
          console.log(this.newComprobante);
          console.log(JSON.parse(aux));
          this.newComprobante = JSON.parse(aux);
          this.newComprobante.fechaGeneracion = new Date(this.newComprobante.fechaGeneracion);
          this.calcularTotalComprobante();
          this.observacionComprobante = this.newComprobante.observacion;
          this.comprobanteForm.patchValue({ descripcion: this.newComprobante.observacion });
          this.comprobanteForm.patchValue({ descripcionTemp: this.newComprobante.observacion });
          this.fechaComprobante = this.newComprobante.fechaGeneracion.toISOString().substring(0, 10);
        }
      } catch (error) {
        this.toastr.error("Error al obtener el comprobante en la url: " + JSON.stringify(error), 'Business Control!');
      }
    });
    this.initialiceComponent();
    this.getUserLogget();
  }

  calcularTotalComprobante(): void {
    console.log("calculando")
    this.newComprobante.total = 0;
    this.newComprobante.conceptos.forEach(t => this.newComprobante.total += t.valor);
  }

  private initialiceComponent() {
    this.config = this.businessService.getConfiguracion();
    if (!this.config || this.config.bienes.length == 0) {
      this.router.navigate(['/business-control-settings']);
      return;
    }
    if (this.config.bienes.length > 0) {
      const bienId = this.businessService.getSelectedId() ?? this.config.bienes[0]?.id;
      this.onBienSelect(bienId);
    }
    this.firmaDigital = this.config.contratante.firmaDigital;
    this.InicializarComprobante();
    this.metodoPagoComprobante = this.config.mediosPago[0];
  }

  Login(): void {
    this.googleService.signIn().then(config => {
      this.userLogger = config.user;
      if (config.data) {
        this.ngOnInit();
      }
    })
  }

  Logout(): void {
    this.googleService.signOut();
    this.userLogger = null;
  }


  getUserLogget(): void {
    this.googleService.getUser().then(config => {
      this.userLogger = config?.user;
    })
  }


  onBienSelect(bienId: string): void {
    this.selectedBienId = bienId;
    this.businessService.setSelectedId(bienId);
    this.selectedBien = this.config.bienes.find(v => v.id === bienId) || null;
    if (this.selectedBien) {
      this.conceptosByContrato = this.config.conceptos.filter(t => t.tipo == this.selectedBien.contratoActivo.tipo);
      this.onConceptoSelected(this.conceptosByContrato[0].descripcion);
    }
    this.comprobanteToPdf = null;
    setTimeout(() => {
      this.comprobanteToPdf = this.selectedBien.comprobantes[0];
      this.renderizarGraficos();
    }, 100);
  }

  onConceptoSelected(descripcion: string): void {
    const concepto = this.config.conceptos.find(v => v.descripcion === descripcion);
    if (concepto) {
      this.comprobanteForm.patchValue({ valor: concepto.valor });
      this.comprobanteForm.patchValue({ descripcionTemp: concepto.descripcion });
      this.comprobanteForm.patchValue({ descripcion: concepto.descripcion });
    }
  }


  addConcepto(): void {
    this.comprobanteForm.patchValue({ submitted: true });
    if (this.comprobanteForm.valid) {
      const formValues = this.comprobanteForm.value;
      const newConcepto: ConceptoComprobante = {
        descripcion: formValues.descripcion,
        valor: formValues.valor
      };
      this.newComprobante.conceptos.push(newConcepto);
      this.comprobanteForm.reset();
    }
  }

  removeConcepto(index: number): void {
    this.newComprobante.conceptos.splice(index, 1);
    this.calcularTotalComprobante();
  }

  async generateComprobante(): Promise<void> {
    if (this.selectedBien) {
      if (!this.fechaComprobante) {
        this.toastr.warning("Debes seleccionar la fecha del comprobante", 'Business Control!');
        return
      }
      if (this.newComprobante.total <= 0) {
        this.toastr.warning("El valor del comprobante debe ser mayor a cero", 'Business Control!');
        return
      }
      this.newComprobante.observacion = this.observacionComprobante;
      this.newComprobante.medioPago = this.metodoPagoComprobante;
      this.newComprobante.fechaGeneracion = this.businessService.instanceDateFromString(this.fechaComprobante);
      await this.businessService.addComprobante(this.selectedBien.id, this.newComprobante);
      this.ngOnInit();
      this.toastr.success('Comprobante generado correctamente', 'Business Control!');
    }
  }

  registerServicio(): void {
    this.servicioForm.patchValue({ submitted: true });
    if (this.selectedBien && this.servicioForm.valid) {
      const newServicio: ReciboServicio = {
        tipo: this.servicioForm.value.tipo,
        valor: this.servicioForm.value.valor,
        fechaGeneracion: this.servicioForm.value.fechaGeneracion,
        fechaPago: this.servicioForm.value.fechaPago
      };
      this.businessService.addServicioPublico(this.selectedBien.id, newServicio);
      this.servicioForm.reset();
      this.ngOnInit();
      this.toastr.success('Servicio registrado correctamente', 'Business Control!');
    }
  }

  renderizarGraficos(): void {
    this.generarGraficaIngresosVsGastos();
  }

  generarGraficaIngresosVsGastos(): void {
    const selectedBien = this.selectedBien;

    // Crear un mapa para agrupar los datos por mes
    const labelsMap = new Map<string, { ingresos: number, aguagastos: number, luzgastos: number, gasgastos: number, internetgastos: number, otrosgastos: number }>();

    // Procesar comprobantes para agrupar ingresos por mes
    selectedBien.comprobantes.forEach(comprobante => {
      const monthYear = new Date(comprobante.fechaGeneracion).toLocaleString('default', { month: 'short', year: 'numeric' });
      if (!labelsMap.has(monthYear)) {
        labelsMap.set(monthYear, { ingresos: 0, aguagastos: 0, luzgastos: 0, gasgastos: 0, internetgastos: 0, otrosgastos: 0 });
      }
      const data = labelsMap.get(monthYear);
      data.ingresos += comprobante.total;
    });

    // Procesar servicios pblicos para agrupar gastos por tipo y mes
    selectedBien.serviciosPublicos.forEach(servicio => {
      const monthYear = new Date(servicio.fechaGeneracion).toLocaleString('default', { month: 'short', year: 'numeric' });
      if (!labelsMap.has(monthYear)) {
        labelsMap.set(monthYear, { ingresos: 0, aguagastos: 0, luzgastos: 0, gasgastos: 0, internetgastos: 0, otrosgastos: 0 });
      }
      const data = labelsMap.get(monthYear);
      switch (servicio.tipo) {
        case 'Agua':
          data.aguagastos += servicio.valor;
          break;
        case 'Luz':
          data.luzgastos += servicio.valor;
          break;
        case 'Gas':
          data.gasgastos += servicio.valor;
          break;
        case 'Internet':
          data.internetgastos += servicio.valor;
          break;
        case 'Otro':
          data.otrosgastos += servicio.valor;
          break;
      }
    });


    this.updateChart(labelsMap);

  }

  updateChart(labelsMap: Map<string, { ingresos: number, aguagastos: number, luzgastos: number, gasgastos: number, internetgastos: number, otrosgastos: number }>): void {
    // Convertir el mapa a arrays para labels y datasets
    const labels = Array.from(labelsMap.keys());
    const ingresos = labels.map(label => labelsMap.get(label).ingresos);
    const aguagastos = labels.map(label => labelsMap.get(label).aguagastos);
    const luzgastos = labels.map(label => labelsMap.get(label).luzgastos);
    const gasgastos = labels.map(label => labelsMap.get(label).gasgastos);
    const internetgastos = labels.map(label => labelsMap.get(label).internetgastos);
    const otrosgastos = labels.map(label => labelsMap.get(label).otrosgastos);

    const data = {
      labels: labels,
      datasets: [
        { label: 'Arriendo', data: ingresos, backgroundColor: '#4caf50', fill: false }
      ]
    }

    if (this.selectedBien.tipo == 'Arrendamiento') {
      data.datasets.push({ label: 'Agua', data: aguagastos, backgroundColor: '#2196f3', fill: false },
        { label: 'Luz', data: luzgastos, backgroundColor: '#ffeb3b', fill: false },
        { label: 'Gas', data: gasgastos, backgroundColor: '#f44336', fill: false },
        { label: 'Internet', data: internetgastos, backgroundColor: '#9c27b0', fill: false },
        { label: 'Otros', data: otrosgastos, backgroundColor: '#ff9800', fill: false })
    }

    if (!this.chart) {
      // Generar el grfico
      this.chart = new Chart('canvas', {
        type: 'line',
        data: data,
        options: {
          responsive: true,
          scales: {
            // xAxes: [{
            //   display: false // Oculta los labels en el eje X
            // }],
            yAxes: [{
              display: false, // Oculta los labels en el eje Y
              ticks: {
                beginAtZero: true
              }
            }]
          },
          legend: {
            // display: false // Opcional: Oculta la leyenda si solo quieres ver las lneas
          }
        }
      });
    }
    this.chart.data = data;

    // Actualiza el grfico
    this.chart.update();
  }

  generatePDF(comprobante: ComprobanteComponent) {
    this.comprobanteToPdf = null;
    comprobante.generandoPdf = true;
    setTimeout(() => {
      this._generatePdf(comprobante);
    }, 100);
  }

  sharedPDF(comprobante: ComprobanteComponent) {
    this.comprobanteToPdf = null;
    comprobante.compartiendoPdf = true;
    setTimeout(() => {
      this._shareByAppsFromSO(comprobante);
    }, 100);
  }

  sharedUrl(comprobante: ComprobanteComponent) {
    this.comprobanteToPdf = null;
    comprobante.compartiendoUrl = true;
    setTimeout(() => {
      this._sharedUrlCorta(comprobante);
    }, 100);
  }

  showComprobante(comprobante: ComprobanteComponent) {
    const url = this.businessService.generateUrlFirmada(comprobante);
    window.open(url, '_blank');
  }

  private _generatePdf(comprobante: ComprobanteComponent) {
    this.comprobanteToPdf = comprobante;
    setTimeout(async () => {
      if (this.pdfContent) {
        await this.pdfService.generatePDF(`Comprobante Nro ${comprobante.id}`, this.pdfContent.nativeElement);
        comprobante.generandoPdf = false;
        this.toastr.info('Pdf generado con exto', 'Business Control!');
      } else {
        this.toastr.error('El contenido del PDF no est disponible.', 'Business Control!');
      }
    }, 0);
  }

  private _shareByAppsFromSO(comprobante: ComprobanteComponent) {
    try {
      this.comprobanteToPdf = comprobante;
      setTimeout(() => {
        this._compartirPdf(comprobante);
      }, 0);
    } catch (error) {
      this.toastr.error('Error al intentar compartir el archivo : ' + JSON.stringify(error), 'Business Control!');
    }
  }

  private _compartirPdf(comprobante: ComprobanteComponent) {
    try {
      if (this.pdfContent) {
        // this.pdfService.generatePDF(comprobante.id, this.pdfContent.nativeElement);
        this.pdfService.getPdfInBlob(comprobante.id, this.pdfContent.nativeElement).then((pdfBlob: Blob) => {
          this._sharedPdf(pdfBlob, comprobante);
          comprobante.compartiendoPdf = false;
        });
      } else {
        this.toastr.error('El contenido del PDF no est disponible.', 'Business Control!');
      }
    } catch (error) {
      this.toastr.error('Error al compartir el archivo : ' + JSON.stringify(error), 'Business Control!');
    }
  }

  private _sharedUrlCorta(comprobante: ComprobanteComponent) {
    if (navigator.share) {
      navigator.share({
        title: `Comprobante Nro ${comprobante.id}.pdf`,
        text: `Hola ${comprobante.bien.contratoActivo.nombre},\n\nEspero que te encuentres bien.\n\nTe comparto con gusto el comprobante de pago correspondiente al periodo del ${this.businessService.formatoInternacional(comprobante.periodo.inicio)} al ${this.businessService.formatoInternacional(comprobante.periodo.fin)}.\n${comprobante.urlCorta}\n\nEste comprobante verifica el cumplimiento de nuestras obligaciones y proporciona detalles completos sobre la transaccin realizada.\n\nSi tienes alguna pregunta o necesitas informacin adicional, no dudes en contactarme.\n\nSaludos cordiales,\n${comprobante.contratante.nombre}`,
      }).then(() => {
        comprobante.compartiendoUrl = false;
        this.toastr.info('Se compartio correctamente la url!', 'Business Control!');
      }).catch((error) => {
        this.toastr.error('Error compartiendo la url: ' + JSON.stringify(error), 'Business Control!');
      });
    } else {
      this.toastr.error('La funcin de compartir no est disponible en este navegador (URL).', 'Business Control!');
    }
  }

  private _sharedPdf(pdfBlob: Blob, comprobante: Comprobante) {
    const file = new File([pdfBlob], `Comprobante Nro ${comprobante.id}.pdf`, { type: 'application/pdf' });
    if (navigator.share) {
      navigator.share({
        title: `Comprobante Nro ${comprobante.id}.pdf`,
        text: `Hola ${comprobante.bien.contratoActivo.nombre},\n\nEspero que te encuentres bien.\n\nTe comparto con gusto el comprobante de pago correspondiente al periodo del ${this.businessService.formatoInternacional(comprobante.periodo.inicio)} al ${this.businessService.formatoInternacional(comprobante.periodo.fin)}. Este comprobante verifica el cumplimiento de nuestras obligaciones y proporciona detalles completos sobre la transaccin realizada.\n\nSi tienes alguna pregunta o necesitas informacin adicional, no dudes en contactarme.\n\nSaludos cordiales,\n${comprobante.contratante.nombre}`,
        files: [file]
      }).then(() => {
        this.toastr.info('Pdf generado con exto, compartelo!', 'Business Control!');
      }).catch((error) => {
        this.toastr.error('Error compartiendo el archivo: ' + JSON.stringify(error), 'Business Control!');
      });
    } else {
      this.toastr.error('La funcin de compartir no est disponible en este navegador.', 'Business Control!');
    }
  }

}

interface ComprobanteComponent extends Comprobante {
  generandoPdf: boolean;
  compartiendoPdf: boolean;
  compartiendoUrl: boolean;
}