import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TransactionFromGoogle } from 'src/app/services/google-services/google.service';

@Component({
  selector: 'app-account-control-transaction-card',
  templateUrl: './account-control-transaction-card.component.html',
  styleUrls: ['./account-control-transaction-card.component.css']
})
export class AccountControlTransactionCardComponent implements OnInit {
  @Input()
  public transaction: TransactionFromGoogle;

  @Input()
  public showOriginalBody: boolean = false;

  @Input()
  public showButtons: boolean = true;

  constructor() { }

  ngOnInit(): void {
  }

  @Output()
  onRegisterCategory = new EventEmitter<TransactionFromGoogle>();

  @Output()
  onRegisterAccount = new EventEmitter<TransactionFromGoogle>();

  @Output()
  onEditTransaction = new EventEmitter<TransactionFromGoogle>();

  openManagementCategories() {
    this.onRegisterCategory.emit(this.transaction);
  }

  openCreateAccount() {
    this.onRegisterAccount.emit(this.transaction);
  }

  openEditTransaction() {
    this.onEditTransaction.emit(this.transaction);
  }


}
