import { OnInit } from '@angular/core';
import { GoogleService, UserGoogle, UserLoggedWithSettingsData } from 'src/app/services/google-services/google.service';

export abstract class BaseComponent {
  static contador = 0;
  public configFromGoogle: UserLoggedWithSettingsData;

  constructor(protected googleService: GoogleService) { }

  protected init(): void {
    this._init();
  };

  private _init() {
    console.log("ENTRO AQUIIIIII...", BaseComponent.contador++);
    this.initialiceComponent();
    this.getUserLogget();
  }

  protected detroy(): void {
  }

  protected hasDataInGoogle(): boolean {
    if (this.configFromGoogle && this.configFromGoogle.data) {
      return true
    }
    return false;
  };

  protected isLogged(): boolean {
    if (this.configFromGoogle && this.configFromGoogle.user) {
      return true
    }
    return false;
  };

  abstract initialiceComponent(): void;

  abstract syncGoogleDrive(): Promise<void>;

  Login(): void {
    this.googleService.signInForAccountControl().then(async config => {
      this.configFromGoogle = config;
      if (config) {
        await this.syncGoogleDrive();
      }
      this.initialiceComponent();
    })
  }

  Logout(): void {
    this.googleService.signOut();
    this.configFromGoogle = null;
  }

  contador = 0;
  getUserLogget(): void {
    this.contador++;
    this.googleService.getUserForAccountControl().then(async config => {
      console.log('Usuario encontrado...', this.contador, config);
      this.configFromGoogle = config;
      if (config) {
        await this.syncGoogleDrive();
      }
      this.initialiceComponent();
    })
  }

}
