import { Component, OnInit, ViewChild } from '@angular/core';
import { GALLERY_CONF, GALLERY_IMAGE, NgxImageGalleryComponent } from 'ngx-image-gallery';
import { Summary } from 'src/app/entities/summary';
import { SummaryResumeService } from 'src/app/services/summary-resume/summary-resume.service';

@Component({
  selector: 'app-briefcases',
  templateUrl: './briefcases.component.html',
  styleUrls: ['./briefcases.component.css']
})
export class BriefcasesComponent implements OnInit {

  constructor(private _summaryResumeService: SummaryResumeService) { }
  public Summary: Summary;
  ngOnInit() {
    this.getData();
  }

  private getData(): void {
    this._summaryResumeService.GetProfile().subscribe(data => {
      this.Summary = data;
      let contador=0;
      this.images=[];
      this.Summary.Briefcases.forEach(Briefcase=> {
        Briefcase.Images.forEach(image=> {
          image.Index=++contador;
          this.images.push({
            url: "config/"+image.Path, 
            altText: 'image.Path', 
            title: 'image.Path',
            thumbnailUrl: "config/"+image.Path
          })
        });
      });
    });
  }

  @ViewChild(NgxImageGalleryComponent) ngxImageGallery: NgxImageGalleryComponent;
  
  // gallery configuration
  conf: GALLERY_CONF = {
    imageOffset: '0px',
    showDeleteControl: false,
    showImageTitle: false,
  };
    
  // gallery images
  images: GALLERY_IMAGE[] = [
    {
      url: "https://images.pexels.com/photos/669013/pexels-photo-669013.jpeg?w=1260", 
      altText: 'woman-in-black-blazer-holding-blue-cup', 
      title: 'woman-in-black-blazer-holding-blue-cup',
      thumbnailUrl: "https://images.pexels.com/photos/669013/pexels-photo-669013.jpeg?w=60"
    },
    {
      url: "https://images.pexels.com/photos/669006/pexels-photo-669006.jpeg?w=1260", 
      altText: 'two-woman-standing-on-the-ground-and-staring-at-the-mountain', 
      extUrl: 'https://www.pexels.com/photo/two-woman-standing-on-the-ground-and-staring-at-the-mountain-669006/',
      thumbnailUrl: "https://images.pexels.com/photos/669006/pexels-photo-669006.jpeg?w=60"
    },
  ];
 
  // METHODS
  // open gallery
  openGallery(index: number = 0) {
    this.ngxImageGallery.open(index);
  }
    
  // close gallery
  closeGallery() {
    this.ngxImageGallery.close();
  }
    
  // set new active(visible) image in gallery
  newImage(index: number = 0) {
    this.ngxImageGallery.setActiveImage(index);
  }
    
  // next image in gallery
  nextImage(index: number = 0) {
    this.ngxImageGallery.next();
  }
    
  // prev image in gallery
  prevImage(index: number = 0) {
    this.ngxImageGallery.prev();
  }
    
  /**************************************************/
}
