import { Injectable } from '@angular/core';
import { CryptService } from '../crypt/crypt.service';
import { HttpClient } from '@angular/common/http';
import { GoogleService, TransactionFromGoogle, UserLoggedWithSettingsData } from '../google-services/google.service';
import { ToastrService } from 'ngx-toastr';
import { Account, AccountConstant, BudgetSettings, Category, SettingsData, Transaction } from 'src/app/entities/account-control';
import { TransactionOperation } from '../utils/transaction-operation';

@Injectable({
  providedIn: 'root'
})
export class AccountControlService {

  public get storageKey(): string {
    if (window.location.hostname == 'localhost')
      return 'accountDataLocal';
    return 'accountData';
  }
  private storageSelectedBienKey = 'selectedAccountId';

  static isSync = false;

  constructor(private toastr: ToastrService, private googleService: GoogleService, private cryptService: CryptService, private _httpService: HttpClient) { }

  getSettingsData(): SettingsData {
    const data = this._getConfigFromLocalStorage();
    if (data) {
      data.accounts = data.accounts as Account[];
    }
    this.syncWithGoogleDrive(data);
    return data;
  }

  public setSelectedId(bienId: string): void {
    localStorage.setItem(this.storageSelectedBienKey, bienId);
  }

  public getSelectedId(): string {
    return localStorage.getItem(this.storageSelectedBienKey);
  }

  saveAccount(account: Account, userName?: string, email?: string): void {
    let data = this._getConfigFromLocalStorage();
    if (!data) {
      data = this.instanceNewConfig(userName, email);
    }
    const oldAccount = data.accounts.find(t => t.id === account.id);
    if (oldAccount) {
      oldAccount.name = account.name;
      oldAccount.description = account.description;
      oldAccount.balance = account.balance;
      oldAccount.isPrincipal = account.isPrincipal;
    } else {
      data.accounts.push(account);
    }
    this.saveSettingsData(data);
  }

  saveTransaction(transaction: Transaction): void {
    let data = this._getConfigFromLocalStorage();
    if (!data) {
      this.toastr.error("Debe existir al menos una cuenta para poder crear una transaccin", 'Account Control!');
      return;
    }
    if (transaction.type === AccountConstant.TRANSACTION_TYPE_EXPENSE && transaction.amount > 0) {
      transaction.amount = -transaction.amount
    }
    if (!transaction.id) {
      transaction.id = this._generateRandomString(20);
    }
    const oldTransaction = data.transactions.find(t => t.id === transaction.id);
    if (oldTransaction) {
      oldTransaction.description = transaction.description;
      oldTransaction.amount = transaction.amount;
      oldTransaction.originalAccount = transaction.originalAccount;
      oldTransaction.categoryId = transaction.categoryId;
      oldTransaction.date = transaction.date;
      oldTransaction.targetAccountId = transaction.targetAccountId;
      oldTransaction.transactionId = transaction.transactionId;
    } else {
      data.transactions.push(transaction);
    }
    this.saveSettingsData(data);
  }

  private instanceNewConfig(name?: string, email?: string): SettingsData {
    let data: SettingsData;
    data = {
      name: name ?? this._generateRandomString(25),
      accounts: [
        {
          id: AccountConstant.ACCOUNT_BILLETERA,
          name: AccountConstant.ACCOUNT_BILLETERA,
          description: 'Esta cuenta es por default, no la elimine',
          balance: 0,
          balanceLastMonth: 0,
          lastBalance: 0,
          isPrincipal: true,
        }
      ],
      budgetSettings: null,
      transactions: [],
      email: email,
    };
    return data;
  }

  saveBudgetSettings(budgetSettings: BudgetSettings): void {
    let data = this._getConfigFromLocalStorage();
    if (!data) {
      this.toastr.error('Debe existir al menos una cuenta registrada, no se deberia poder configurar presupuesto sin al menos una cuenta creada!!!', 'Account Control!');
      return;
    }
    data.budgetSettings = budgetSettings;
    this.saveSettingsData(data);
  }

  private _generateRandomString(length: number): string {
    return Math.random().toString(36).substring(2, 2 + length);
  }

  public async syncWithGoogleDrive(configLocalStorage: SettingsData, bypass: boolean = false): Promise<void> {
    if (!AccountControlService.isSync || AccountControlService.isSync && !configLocalStorage || bypass) {
      AccountControlService.isSync = true;
      const userLogged = await this.googleService.getUserForAccountControl();
      if (userLogged && userLogged.data) {
        let uploadConfigInGoogle = false;
        if (userLogged.data && !userLogged.data.lastSync) {
          userLogged.data.lastSync = new Date();
          //se manda a subir la data nuevamente a google drive ya que en google drive no hay fecha de modificacion en el json
          uploadConfigInGoogle = true;
        }
        //existe data en google y no hay fecha de modificacion o la fecha de modificacion de localstorage es menor a la de google entonces bajo y almaceno los datos en local
        if (userLogged.data && (!configLocalStorage || !configLocalStorage.lastSync || configLocalStorage.lastSync < userLogged.data.lastSync)) {
          await this.saveSettingsData(userLogged.data, uploadConfigInGoogle);
          this.toastr.success('Informacin syncronizada correctamente desde google drive', 'Account Control!');
          setTimeout(() => {
            window.location.reload();
          }, 1500);
        }
      }
      //si no hay data en google entonces subimos la que esta en localstorage
      if (userLogged && !userLogged.data) {
        await this.uploadConfigWithGoogleDrive();
      }
    }
  }

  private _getConfigFromLocalStorage(): SettingsData {
    const data = this._readConfig();
    const response = data ? JSON.parse(data) as SettingsData : null;
    if (response) {
      if (response.lastSync) {
        response.lastSync = new Date(response.lastSync);
      }
      if (response.lastSyncTransactions) {
        response.lastSyncTransactions = new Date(response.lastSyncTransactions);
      }
      response.transactions.forEach(t => {
        t.date = new Date(t.date);
      })
      response.accounts.forEach(account => {
        if (account.lastTransaction) {
          account.lastTransaction.date = new Date(account.lastTransaction.date);
        }
      });
    }
    return response;
  }

  private _readConfig() {
    return localStorage.getItem(this.storageKey);
  }

  async saveSettingsData(config: SettingsData, syncWithGoogle: boolean = true): Promise<SettingsData> {
    config.lastSync = new Date();
    localStorage.setItem(this.storageKey, JSON.stringify(config));
    const isLogged = await this.googleService.isUserLogged()
    if (isLogged && syncWithGoogle) {
      await this.uploadConfigWithGoogleDrive();
    }
    return config;
  }
  /**
   * Este metodo se utiliza debido a que desde el front los input date arrojan un string como valor y en tiempo de ejecucion este string en formato "2024-07-18" toca convertirlo a date
   * @param dateString fecha en formato string ("2024-07-18")
   * @returns fecha
   */
  instanceDateFromString(dateString: string): Date {
    // Extraer ao, mes y da del string
    const [year, month, day] = dateString.split('-').map(Number);

    // Crear la fecha manualmente usando el constructor de Date con los componentes individuales
    // El mes en el constructor Date es 0-indexed, por eso restamos 1
    return new Date(year, month - 1, day);
  }

  downloadConfig(): void {
    const config = this._getConfigFromLocalStorage();
    if (config) {
      // Crea un blob con el contenido JSON
      const blob = new Blob([JSON.stringify(config, null, 2)], { type: 'application/json' });

      // Crea un enlace para la descarga
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = `Account-Control${config.name}.json`;

      // Simula un clic en el enlace para iniciar la descarga
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  }

  private async uploadConfigWithGoogleDrive(): Promise<void> {
    const config = this._getConfigFromLocalStorage();
    if (config) {
      // Crea un blob con el contenido JSON
      const blob = new Blob([JSON.stringify(config, null, 2)], { type: 'application/json' });
      const fileName = `AC-Main-${config.name}.json`;

      // Llama al mtodo para subir el archivo a Google Drive
      await this.googleService.uploadFileWithFolderDetection(blob, fileName, true);
    }
  }

  deleteData(): void {
    localStorage.removeItem(this.storageKey);
  }

  // Define el formato internacional deseado
  formatoInternacional(date: Date): string {

    // Si date es una cadena, convirtelo a un objeto Date
    date = typeof date === 'string' ? new Date(date) : date;
    // Define el formato internacional deseado
    const _formatoInternacional = new Intl.DateTimeFormat('es-CO', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
    return _formatoInternacional.format(date);
  }

  async syncTransactions(userLogged: UserLoggedWithSettingsData): Promise<SettingsData> {
    console.log('Sincronizando transacciones...', userLogged.user.username);
    let config = this._getConfigFromLocalStorage();
    let defaultStartDate = new Date();
    // Resta 3 meses a la fecha de inicio para buscar transacciones de hace tres meses
    defaultStartDate.setMonth(defaultStartDate.getMonth() - 3);
    const newTransactions = await this.googleService.getTransactionsSince(config?.lastSyncTransactions ?? defaultStartDate);
    if (newTransactions.length > 0) {
      config = await this._syncTransactions(config, userLogged, newTransactions);
    }
    return this.getTransactions(config);
  }

  private async _syncTransactions(config: SettingsData, userLogged: UserLoggedWithSettingsData, newTransactions: TransactionFromGoogle[]) {
    if (!config) {
      config = this.instanceNewConfig(userLogged.user.username, userLogged.user.email);
    }
    if (!config.transactions) {
      config.transactions = [];
    }
    config.transactions.push(...newTransactions);

    config.lastSyncTransactions = new Date();
    config.transactions = config.transactions.sort((a, b) => b.date.getTime() - a.date.getTime());

    await this.saveSettingsData(config);
    this.toastr.success('[' + (newTransactions.length) + '] Transacciones sincronizadas correctamente', 'Account Control!');

    return config;
  }

  private getCategory(config: SettingsData, t: TransactionFromGoogle, showToastr = false): Category {
    const category = config?.budgetSettings?.categories?.find(cat => cat.keyWords.some(key => t.description.toLowerCase().includes(key.toLowerCase())))
    if ((!category || (category && !category.accountId)) && !t.originalAccount) {
      if (showToastr && config.budgetSettings) {
        this.toastr.warning(`No se encontro categora para la transaccin "${t.description}" por un monto de ${t.amount}`, 'Account Control!');
      }
      return;
    }
    return category;
  }

  getTransactions(config?: SettingsData): SettingsData {
    if (!config) {
      config = this.getSettingsData();
      if (!config) {
        config = this.instanceNewConfig();
      }
    }
    const helper = new TransactionOperation();
    let transactionsProcessed = 0;
    config?.budgetSettings?.categories?.forEach(cat => {
      cat.currentAmmount = 0;
    });
    config?.transactions?.forEach(t => {
      const canContinue = this.processTransaction(config, t);
      if (!canContinue) {
        return;
      }
      transactionsProcessed++;
      if (t.targetAccountId) {
        //si tiene una cuenta configurada para el targetAccountId, crea una nueva transaccin de lo contrario no porque si no crearia muchas transferencias de cuentas que no son mias
        //esto lo hice solo para mi tarjeta de crdito o cuando cree alguna cuenta cuando le preste a amigos etc.
        const auxaccount = this.getAccount(config, t.targetAccountId);
        if (!auxaccount) {
          return;
        }
        const newTransaction = helper.createTransactionIfIsTranfer(t);
        if (newTransaction) {
          this.processTransaction(config, newTransaction);
          config.transactions.push(newTransaction);
          transactionsProcessed++;
        }
      }
    });
    if (transactionsProcessed > 0) {
      config.transactions = config.transactions.sort((a, b) => b.date.getTime() - a.date.getTime());
      this.saveSettingsData(config);
      this.toastr.info('[' + (transactionsProcessed) + '] Transacciones procesadas correctamente', 'Account Control!');
    }
    return config;
  }

  private processTransaction(config: SettingsData, t: Transaction): boolean {
    if (!t.canProcessed) {
      // Transaccin no puede ser procesada
      const anotherTransaction = new TransactionOperation().getTransaction(t.originalBody, t.id);
      if (!anotherTransaction.canProcessed)
        return;
      // Asignar campos de anotherTransaction a t
      Object.assign(t, anotherTransaction);
    }
    const category = this.getCategory(config, t, false);
    if (category) {
      t.categoryId = category.id;
      const currentDate = new Date().getFullYear() + '-' + (new Date().getMonth() + 1);
      const dateOfTransaction = t.date.getFullYear() + '-' + (t.date.getMonth() + 1);
      if (currentDate == dateOfTransaction) {
        category.currentAmmount += Math.abs(t.amount);
      }
    }
    if (!t.originalAccount && category) {
      t.originalAccount = category.accountId;
    }
    if (t.isProcessed) {
      // Transaccin ya procesada
      return;
    }
    const accountId = t.originalAccount ?? category?.accountId;
    if (!accountId) {
      return;
    }
    const account = this.getAccount(config, accountId);
    if (!account) {
      if (config.budgetSettings) {
        // this.toastr.warning(`No se encontro cuenta para la transaccin "${t.description}" por un monto de ${t.amount}`, 'Account Control!');
      }
      return;
    }

    if (account.lastTransaction) {
      const dateAccount = account.lastTransaction.date.getFullYear() + account.lastTransaction.date.getMonth();
      const dateTransaction = t.date.getFullYear() + t.date.getMonth();
      if (dateTransaction > dateAccount) {
        account.balanceLastMonth = account.balance;
      }
    }
    t.accountId = account.id;
    account.lastBalance = account.balance;
    account.balance += t.amount;
    account.lastTransaction = t;
    t.isProcessed = true;

    return true;
  }

  private getAccount(config: SettingsData, accountId: string) {
    return config.accounts.find(acc => acc.id.toLowerCase().includes(accountId.toLowerCase()));
  }
}