import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BudgetSettings, Category, SettingsData, AccountConstant } from 'src/app/entities/account-control';
import { AccountControlService } from 'src/app/services/account-control/account-control.service';
import { TransactionFromGoogle } from 'src/app/services/google-services/google.service';

@Component({
  selector: 'app-account-control-management-categories',
  templateUrl: './account-control-management-categories.component.html',
  styleUrls: ['./account-control-management-categories.component.css']
})
export class AccountControlManagementCategoriesComponent implements OnInit {

  constructor(private accountService: AccountControlService) { }


  private _transaction: TransactionFromGoogle;

  @Input()
  set transaction(value: TransactionFromGoogle) {
    this._transaction = value;
    this.onTransactionChange(value); // Llama a un mtodo cuando el valor cambia
  }

  onTransactionChange(value: TransactionFromGoogle) {
    if (value) {
      if (value.type === AccountConstant.TRANSACTION_TYPE_INCOME || value.type === AccountConstant.TRANSACTION_TYPE_EXPENSE) {
        this.activeTab = value.type;
        this.addNewCategory();
      }
    }
  }

  get transaction(): TransactionFromGoogle {
    return this._transaction;
  }

  private _settionsData: SettingsData;
  @Input()
  set settionsData(value: SettingsData) {
    if (value) {
      this._settionsData = JSON.parse(JSON.stringify(value));
      this.onSettionsDataChange(value); // Llama a un mtodo cuando el valor cambia
    }
  }

  onSettionsDataChange(value: SettingsData) {
    if (value && value.budgetSettings) {
      this.categories = value.budgetSettings.categories.map(t => {
        return { ...t, newKeyword: '' };
      });
      this.budgetSettings = value.budgetSettings;
      if (this._transaction) {
        this.onTransactionChange(this._transaction);
      }
    }
  }

  get settionsData(): SettingsData {
    return this._settionsData;
  }

  accountsId(): string[] {
    return this.settionsData?.accounts?.map(a => a.id) ?? [];
  }

  @Output()
  onFinish = new EventEmitter<BudgetSettings>();

  ngOnInit(): void {
  }

  activeTab: 'income' | 'expense' = 'income';
  newKeyword: string = '';
  categories: CategoryInComponent[] = [];
  budgetSettings: BudgetSettings = {
    categories: [],
    totalIncome: 0,
    totalExpense: 0,
    savingsGoal: 0
  };

  setActiveTab(type: 'income' | 'expense') {
    this.activeTab = type;
  }

  get categoriesOfCurrentTab(): Category[] {
    return this.getCategoriesByType(this.activeTab);
  }

  getCategoriesByType(type: 'income' | 'expense') {
    return this.categories.filter(category =>
      category.type === (type === 'income' ? AccountConstant.TRANSACTION_TYPE_INCOME : AccountConstant.TRANSACTION_TYPE_EXPENSE)).sort((a, b) => a.order - b.order);
  }

  addNewCategory() {
    let groupOfOrder = this.getGroupOfOrder(this.activeTab);
    const newCategory: CategoryInComponent = {
      id: `${this.activeTab}_${Date.now()}`,
      name: '',
      keyWords: [],
      type: this.activeTab === 'income' ? AccountConstant.TRANSACTION_TYPE_INCOME : AccountConstant.TRANSACTION_TYPE_EXPENSE,
      budget: 0,
      accountId: '',
      order: groupOfOrder + this.getCategoriesByType(this.activeTab).length,
      newKeyword: ''  // Aade la propiedad temporal de entrada de keyword
    };
    this.categories.push(newCategory);
    this.updateBudgetSummary();
  }

  private getGroupOfOrder(type: string) {
    let groupOfOrder = 1000;
    if (type === AccountConstant.TRANSACTION_TYPE_INCOME) {
      groupOfOrder = 2000;
    }
    return groupOfOrder;
  }

  addKeyword(category: CategoryInComponent) {
    if (category.newKeyword && !category.keyWords.includes(category.newKeyword)) {
      category.keyWords.push(category.newKeyword.trim());
      category.newKeyword = ''; // Limpiar el input de esta fila especfica
      this.updateBudgetSummary();
    }
  }

  // Mover una categora hacia arriba
  moveCategoryUp(index: number): void {
    if (index > 0) {
      const indexReal = this.getIndexReal(index);
      [this.categories[indexReal - 1], this.categories[indexReal]] = [this.categories[indexReal], this.categories[indexReal - 1]];
      const groupOfOrder = this.getGroupOfOrder(this.categories[indexReal].type);
      this.categories[indexReal].order = groupOfOrder + indexReal;
      this.categories[indexReal - 1].order = groupOfOrder + indexReal - 1;
    }
  }



  // Mover una categora hacia abajo
  moveCategoryDown(index: number): void {
    if (index < this.getCategoriesByType(this.activeTab).length - 1) {
      const indexReal = this.getIndexReal(index);
      [this.categories[indexReal + 1], this.categories[indexReal]] = [this.categories[indexReal], this.categories[indexReal + 1]];
      const groupOfOrder = this.getGroupOfOrder(this.categories[indexReal].type);
      this.categories[indexReal].order = groupOfOrder + indexReal;
      this.categories[indexReal + 1].order = groupOfOrder + indexReal + 1;
    }
  }

  private getIndexReal(index: number): number {
    if (this.activeTab === AccountConstant.TRANSACTION_TYPE_INCOME) {
      return index + (this.getCategoriesByType(AccountConstant.TRANSACTION_TYPE_INCOME).length);
    }
    return index;
  }

  removeKeyword(category: CategoryInComponent, keyword: string) {
    category.keyWords = category.keyWords.filter(k => k !== keyword);
    this.updateBudgetSummary();
  }

  removeCategory(category: CategoryInComponent) {
    this.categories = this.categories.filter(k => k.id !== category.id);
    this.updateBudgetSummary();
  }

  saveAllCategories() {
    // Lgica para guardar todas las categoras en la base de datos o backend
    this.updateBudgetSummary();
    this.accountService.saveBudgetSettings(this.budgetSettings);
    this.onFinish.emit(this.budgetSettings);
  }

  updateBudgetSummary() {
    this.categories.forEach(category => {
      let groupOfOrder = this.getGroupOfOrder(category.type);
      category.order = groupOfOrder + this.categories.indexOf(category);
      category.id = `${category.type}_${category.name}`.replace(/ /g, "").toLowerCase();
    });
    this.budgetSettings.categories = this.categories;
    this.budgetSettings.totalIncome = this.budgetSettings.categories
      .filter(c => c.type === AccountConstant.TRANSACTION_TYPE_INCOME)
      .reduce((sum, category) => sum + category.budget, 0);

    this.budgetSettings.totalExpense = this.budgetSettings.categories
      .filter(c => c.type === AccountConstant.TRANSACTION_TYPE_EXPENSE)
      .reduce((sum, category) => sum + category.budget, 0);
    this.budgetSettings.savingsGoal = this.budgetSettings.totalIncome - this.budgetSettings.totalExpense;
  }

  isFormValid() {
    return this.categories.every(category =>
      category.name &&
      category.type &&
      category.keyWords.length > 0
    );
  }

  isFormValidForCurrentTab(type: 'income' | 'expense') {
    return this.getCategoriesByType(type).every(category =>
      category.name &&
      category.type &&
      category.keyWords.length > 0
    );
  }
}

export interface CategoryInComponent extends Category {
  newKeyword: string;
}