import { Transaction, AccountConstant } from "src/app/entities/account-control";
import { TransactionFromGoogle } from "../google-services/google.service";

export class TransactionOperation {

    public getTransaction(body: string, id: string): TransactionFromGoogle {
        const purchasePattern = /[Cc]ompraste\s+COP([\d,.]+)\s+en\s+([\w\s*]+)\s+con\s+tu\s+T\.Cred\s+\*{1,2}([\d]{4}),\s+el\s+(([\d/]+)\s+a\s+las\s+([\d:]+)|([\d:]+)\s+a\s+las\s+([\d/]+))/i;
        const purchasePatternSimple = /[Cc]ompra\s+por\s+COP([\d,.]+)\s+en\s+([\w\s*]+)\s+(\d{2}:\d{2})\.\s+([\d/]+)\s+T\.Cred\s+\*([\d]{4})/i;
        const purchasePatternSimpl2 = /[Cc]ompra\s+por\s+\$([\d,.]+)\s+en\s+([\w\s*]+)\s+(\d{2}:\d{2})\.\s+([\d/]+)\s+T\.Cred\s+\*([\d]{4})/i;
        const transferPattern = /[Tt]ransferiste\s+\$([\d,.]+)\s+desde\s+tu\s+cuenta\s+\*(\d+)\s+a\s+la\s+cuenta\s+(\*\d+)\s+el\s+([\d/]+)\s+a\s+las\s+([\d:]+)/i;
        const transferPatter2 = /[Tt]ransferiste\s+\$([\d,.]+)\s+de\s+tu\s+cuenta\s+\*\*(\d+)\s+la\s+cuenta\s+\*(\d+),\s+el\s+([\d\/]+)\s+([\d:]+)/i
        const transferPatter3 = /[Tt]ransferencia\s+por\s+\$([\d,.]+)\s+desde\s+cta\s+\*(\d+)\s+a\s+cta\s+(\d+)\.\s+([\d\/]+)\s+(\d{2}:\d{2})/i;

        const providerPaymentPattern = /[Pp]ago\s+[Pp]ROVEEDOR\s+de\s+([\w\s]+)\s+por\s+\$([\d,.]+)\s+en\s+su\s+[Cc]uenta\s+[Aa]horros.\s+(\d{2}:\d{2})\s+([\d\/]+)/i;


        const qrTransferPattern = /[Rr]ealizaste\s+una\s+transferencia\s+con\s+QR\s+por\s+\$([\d,.]+),\s+desde\s+cta\s+(\d+)\s+a\s+cta\s+(\d+).\s+([\d\/]+)\s+(\d{2}:\d{2})/i;
        const receivedTransferPattern = /[Rr]ecepcion\s+transferencia\s+de\s+([\w\s]+)\s+por\s+\$([\d,.]+)\s+en\s+la\s+cuenta\s+(\*\d+)\.\s*(\d{2}\/\d{2}\/\d{4})\s+(\d{2}:\d{2})/i;
        const incomePattern = /[Pp]ago\s+de\s+Nomina\s+de\s+([\w\s]+)\s+por\s+\$([\d,.]+)\s+en\s+su\s+Cuenta\s+Ahorros.\s+([\d:]+)\s+([\d/]+)/i;
        const withdrawalPattern = /[Rr]etiraste\s+\$([\d,.]+)\s+en\s+([\w\s*]+)\s+de\s+tu\s+T\.Deb\s+\*\*([\d]{4})\s+el\s+([\d/]+)\s+a\s+las\s+([\d:]+)/i;
        const retiroPattern = /[Rr]etiro\s+por\s+\$([\d,.]+)\s+en\s+([\w_]+)\.\s+Hora\s+(\d{2}:\d{2})\s+([\d\/]+)\s+T\.Deb\s+\*(\d{4})/i;
        const paymentPattern = /[Pp]agaste\s+\$([\d,.]+)\s+a\s+([\w\s]+)\s+desde\s+tu\s+producto\s+(\*\d+)\s+el\s+([\d\/]+)\s+(\d{2}:\d{2})/i;
        const affiliatePurchasePattern = /[Cc]ompra por COP([\d,.]+)\s+en\s+([\w\/.]+)\s+(\d{2}:\d{2})\.\s+([\d\/]+)\s+compra\s+afiliada\s+a\s+T\.Cred\s+\*([\d]{4})/i;
        const affiliatePurchasePatter2 = /[Cc]ompra\s+por\s+\$([\d,.]+)\s+en\s+([\w\/.]+)\s+(\d{2}:\d{2})\.\s+([\d\/]+)\s+compra\s+afiliada\s+a\s+T\.Cred\s+\*(\d{4})/i;
        const notificationPattern = /[Rr]ecibiras \$(\d{1,3}(?:\.\d{3})*,\d{2}) en (\d+) dias habiles en tu Tarjeta Credito \*\*(\d{4}) por parte de ([\w\s]+?)\. (\d{2}):(\d{2}) (\d{2})\/(\d{2})\/(\d{4})/;
        const paymentCreditCardPattern = /[Nn]otificacin [Tt]ransaccional.*Pago\s+de\s+Tarjeta\s+de\s+Credito\s+por\s+\$([\d,.]+)\s+desde\s+cta\s+\*(\d+)\s+a\s+la\s+tarjeta\s+\*(\d+).\s+([\d/]+)\s+(\d{2}:\d{2})/i;

        // Patrn para las notificaciones de compras de Bancolombia


        let type: 'income' | 'expense' | 'transfer' = 'expense';
        let accountId = '';
        let amount = 0;
        let date: Date | string = new Date();
        let description = '';
        let categoryId = '';
        let targetAccountId = '';

        if (purchasePattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(purchasePattern);
            if (matches) {
                amount = this.parseAmount(matches[1]);
                description = `Compra en ${matches[2]}`;
                accountId = `*${matches[3]}`;
                if (!matches[5]) {
                    date = this.parseDateTime(matches[8], matches[7]);
                } else {
                    date = this.parseDateTime(matches[5], matches[6]);
                }
            }
        } else if (purchasePatternSimple.test(body)) {
            const matches = body.match(purchasePatternSimple);
            if (matches) {
                type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
                amount = this.parseAmount(matches[1]);
                description = `Compra en ${matches[2]}`;
                date = this.parseDateTime(matches[4], matches[3]);
                accountId = `*${matches[5]}`;
            }
        } else if (purchasePatternSimpl2.test(body)) {
            const matches = body.match(purchasePatternSimpl2);
            if (matches) {
                type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
                amount = this.parseAmount(matches[1]);
                description = `Compra en ${matches[2]}`;
                date = this.parseDateTime(matches[4], matches[3]);
                accountId = `*${matches[5]}`;
            }
        } else if (transferPattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(transferPattern);
            if (matches) {
                amount = this.parseAmount(matches[1], 1);
                accountId = `*${matches[2]}`;
                targetAccountId = matches[3];
                description = `Transferencia a la cuenta ${targetAccountId}`;
                date = this.parseDateTime(matches[4], matches[5]);
            }
        } else if (transferPatter2.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(transferPatter2);
            if (matches) {
                amount = this.parseAmount(matches[1], 1);
                accountId = `*${matches[2]}`;
                targetAccountId = matches[3];
                description = `Transferencia a la cuenta ${targetAccountId}`;
                date = this.parseDateTime(matches[4], matches[5]);
            }
        } else if (qrTransferPattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(qrTransferPattern);
            if (matches) {
                amount = this.parseAmount(matches[1]);
                accountId = `*${matches[2]}`;
                targetAccountId = matches[3];
                description = `Transferencia con QR a la cuenta ${targetAccountId}`;
                date = this.parseDateTime(matches[4], matches[5]);
            }
        } else if (receivedTransferPattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_INCOME;
            const matches = body.match(receivedTransferPattern);
            if (matches) {
                description = `transferencia de ${matches[1].trim()}`;
                amount = this.parseAmount(matches[2], 1);
                accountId = matches[3];
                date = this.parseDateTime(matches[4], matches[5]);
            }
        } else if (incomePattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_INCOME;
            const matches = body.match(incomePattern);
            if (matches) {
                description = `Pago de nmina de ${matches[1]}`;
                amount = this.parseAmount(matches[2]);
                date = this.parseDateTime(matches[4], matches[3]);
            }
        } else if (withdrawalPattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(withdrawalPattern);
            if (matches) {
                amount = this.parseAmount(matches[1]);
                description = `Retiraste en ${matches[2]}`;
                accountId = `*${matches[3]}`;
                date = this.parseDateTime(matches[4], matches[5]);
                targetAccountId = AccountConstant.ACCOUNT_BILLETERA;
            }
        } else if (paymentPattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(paymentPattern);
            if (matches) {
                amount = this.parseAmount(matches[1]);
                description = `Pago a ${matches[2]}`;
                accountId = matches[3];
                date = this.parseDateTime(matches[4], matches[5]);
            }
        } else if (affiliatePurchasePattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(affiliatePurchasePattern);
            if (matches) {
                amount = this.parseAmount(matches[1]);
                description = `Compra en ${matches[2]}`;
                accountId = `*${matches[5]}`;
                date = this.parseDateTime(matches[4], matches[3]);
            }
        } else if (affiliatePurchasePatter2.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(affiliatePurchasePatter2);
            if (matches) {
                amount = this.parseAmount(matches[1]);
                description = `Compra en ${matches[2]}`;
                accountId = `*${matches[5]}`;
                date = this.parseDateTime(matches[4], matches[3]);
            }
        } else if (notificationPattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_INCOME;
            const matches = body.match(notificationPattern);
            if (matches) {
                amount = this.parseAmount(matches[1]);
                accountId = `*${matches[3]}`;
                description = `Recibirs en tu Tarjeta de Crdito por parte de ${matches[4].trim()}`;
                date = this.parseDateTime(`${matches[7]}/${matches[8]}/${matches[9]}`, `${matches[5]}:${matches[6]}`);
            }
        } else if (paymentCreditCardPattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(paymentCreditCardPattern);
            if (matches) {
                amount = this.parseAmount(matches[1]);
                accountId = `*${matches[2]}`;
                targetAccountId = `*${matches[3]}`;
                description = `Pago de T.Cred Nro ${targetAccountId}`;
                date = this.parseDateTime(matches[4], matches[5]);
            }
        }
        else if (retiroPattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(retiroPattern);
            if (matches) {
                amount = this.parseAmount(matches[1]);
                description = `Retiro en ${matches[2]}`;
                date = this.parseDateTime(matches[4], matches[3]);
                accountId = `*${matches[5]}`;
                targetAccountId = AccountConstant.ACCOUNT_BILLETERA;
            }
        } else if (transferPatter3.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_EXPENSE;
            const matches = body.match(transferPatter3);
            if (matches) {
                amount = this.parseAmount(matches[1], 1);
                accountId = `*${matches[2]}`;
                targetAccountId = `*${matches[3]}`;
                description = `Transferencia a la cuenta ${targetAccountId}`;
                date = this.parseDateTime(matches[4], matches[5]);
            }
        } else if (providerPaymentPattern.test(body)) {
            type = AccountConstant.TRANSACTION_TYPE_INCOME;
            const matches = body.match(providerPaymentPattern);
            if (matches) {
                description = `Pago PROVEEDOR de ${matches[1]}`;
                amount = this.parseAmount(matches[2]);
                accountId = ''; // Si hay un accountId especfico, adelo aqu
                date = this.parseDateTime(matches[4], matches[3]);
            }
        }




        if (type === AccountConstant.TRANSACTION_TYPE_EXPENSE && amount > 0) {
            amount = -amount;
        }
        accountId = accountId.replace(/\*/g, '');
        targetAccountId = targetAccountId.replace(/\*/g, '');

        return { id, accountId: '', originalAccount: accountId, amount, date, categoryId, description, type, originalBody: body, targetAccountId: targetAccountId, canProcessed: (description ? true : false), isProcessed: false };
    }

    public createTransactionIfIsTranfer(transaction: Transaction): Transaction {
        if (transaction.targetAccountId) {
            transaction.transactionId = transaction.id;
            const cloneTransaction = { ...transaction };
            transaction.id = this.generateUniqueId();
            cloneTransaction.id = this.generateUniqueId();
            cloneTransaction.accountId = (transaction.accountId ? transaction.targetAccountId : null);
            cloneTransaction.originalAccount = transaction.targetAccountId;
            cloneTransaction.targetAccountId = transaction.originalAccount;
            cloneTransaction.isProcessed = false;
            cloneTransaction.description = `Transf. Automatica: ${cloneTransaction.description}`;
            cloneTransaction.type = AccountConstant.TRANSACTION_TYPE_INCOME;
            cloneTransaction.amount = cloneTransaction.amount * -1;
            return cloneTransaction;
        }
        return null;
    }

    private parseAmount(amountStr: string, rounded = 100): number {
        if (amountStr.includes(".") && amountStr.includes(","))
            return parseFloat(amountStr.replace(/[,.]/g, '')) / rounded;
        return parseFloat(amountStr.split(".")[0]);
    }

    public parseDateTime(dateStr: string, timeStr: string = '00:00'): Date {
        let day: number, month: number, year: number;
        const [hour, minute] = timeStr.split(':').map(Number);

        // Detect format and parse accordingly
        if (dateStr.includes('/')) {
            const parts = dateStr.split('/');

            if (parts[0].length === 4) {
                // yyyy/mm/dd format
                [year, month, day] = parts.map(Number);
            } else {
                // dd/mm/yyyy format
                [day, month, year] = parts.map(Number);
            }
        } else {
            // throw new Error("Unrecognized date format");
            return new Date();
        }

        // Create date in UTC, then adjust to Colombia time
        const date = new Date(Date.UTC(year, month - 1, day, hour + 5, minute)); // UTC + 5 for Colombia
        date.setUTCHours(date.getUTCHours()); // Final adjustment for Colombia Time
        return date;
    }


    private generateUniqueId(): string {
        return '_' + Math.random().toString(36).substr(2, 9);
    }
}
