export class AccountConstant {
    static TRANSACTION_TYPE_INCOME: 'income' = 'income';
    static TRANSACTION_TYPE_EXPENSE: 'expense' = 'expense';
    static ACCOUNT_BILLETERA: 'Billetera' = 'Billetera';
}

/**
 * Son las cuentas que el usuario tiene, banco, billetera, inversin, crdito
 */
export interface Account {
    id: string;  // Debe corresponder al nmero de cuenta que llegan en los correos, si queremos que de manera automtica se creen transacciones con esta cuenta
    name: string;
    description: string;
    balance: number;  // Saldo actual de la cuenta
    lastBalance: number;  // Saldo inmediatamente anterior
    balanceLastMonth: number;  // Saldo del mes anterior de la cuenta
    isPrincipal: boolean;
    lastTransaction?: Transaction;  // Saldo del mes anterior de la cuenta
}

/**
 * Transacciones realizadas en las cuentas del usuario
 */
export interface Transaction {
    id: string;
    accountId: string;  // Relacin con la cuenta
    originalAccount: string;  // Relacin con la cuenta
    amount: number;
    date: Date;
    categoryId: string;  // Id de la Categora de la transaccin
    originalBody: string
    canProcessed: boolean
    description: string;
    isProcessed: boolean;  // Indica si la transaccin est en proceso de procesamiento
    targetAccountId?: string;
    transactionId?: string;
    type: typeof AccountConstant.TRANSACTION_TYPE_INCOME | typeof AccountConstant.TRANSACTION_TYPE_EXPENSE;  // Tipo de transaccin: ingreso o gasto
}

/**
 * Categoras de ingresos y gastos
 */
export interface Category {
    id: string;//Compuesto de el type y name (type_name)
    order: number;
    name: string;
    keyWords: string[];  // Palabra clave para buscar la categora en el filtro de los correos 
    type: typeof AccountConstant.TRANSACTION_TYPE_INCOME | typeof AccountConstant.TRANSACTION_TYPE_EXPENSE;  // Tipo de transaccin: ingreso o gasto
    budget: number;  // Presupuesto asignado a la categora
    currentAmmount?: number;  // valor absoluto del monto actual en la categora
    accountId: string; //Cuenta de destinoa donde debe ir el dinero
}

/**
 * Configuracin del usuario, incluyendo las cuentas y ltimas sincronizaciones
 */
export interface SettingsData {
    name: string;
    email: string;
    accounts: Account[];
    transactions: Transaction[];  // Lista de transacciones asociadas a la cuenta
    lastSync?: Date;
    lastSyncTransactions?: Date;
    budgetSettings: BudgetSettings;  // Configuracin del presupuesto
}

/**
 * Configuracin del presupuesto, incluyendo categoras y lmites
 */
export interface BudgetSettings {
    categories: Category[];  // Lista de categoras y sus presupuestos
    totalIncome: number;  // Ingresos totales planificados
    totalExpense: number;  // Gastos totales planificados
    savingsGoal?: number;  // Objetivo de ahorro
}
