import { Component, Input, OnInit } from '@angular/core';
import { Comprobante, Contrato, ContratoID } from 'src/app/entities/business-control';
import { BusinessControlService } from 'src/app/services/business-control/business-control.service';
import { CryptService } from 'src/app/services/crypt/crypt.service';
import { PdfService } from 'src/app/services/pdf/pdf.service';
import { QrCodeService } from 'src/app/services/qr-code/qr-code.service';

@Component({
  selector: 'app-business-control-voucher',
  templateUrl: './business-control-voucher.component.html',
  styleUrls: ['./business-control-voucher.component.css']
})
export class BusinessControlVoucherComponent implements OnInit {
  @Input() comprobante: Comprobante;
  @Input() isVerify: boolean;
  host: string = window.location.origin;
  qrCodeDataUrl: string | undefined;
  comprobanteUrl: string = '';
  date: Date = new Date();
  contrato: Contrato;
  contratoID: ContratoID | null;

  constructor(private qrCodeService: QrCodeService, private service: BusinessControlService, private cryptService: CryptService) {
  }

  ngOnInit(): void {
    this.contrato = this.comprobante.bien.contratos.find(t => t.id == this.comprobante.contratoId);
    this.host = window.location.origin;
    this.replaceData();
    try {
      const dencriptado = this.cryptService.decrypt(this.contrato.id);
      this.contratoID = JSON.parse(dencriptado) as ContratoID;
    } catch (error) {
      this.contratoID = null;
    }
  }

  darFormatoCol(date: Date): string {
    return this.service.formatoInternacional(date);
  }

  replaceData() {
    this.comprobanteUrl = this.service.generateUrlFirmada(this.comprobante);
    if (this.comprobante.urlCorta) {
      this.generateQRCode(this.comprobante.urlCorta);
    } else {
      this.generateQRCode(JSON.stringify({
        codigo: this.comprobante.id,
        total: this.comprobante.total,
        numeroContrato: this.comprobante.contratoId
      }));
    }
  }
  generateQRCode(text: string) {
    this.qrCodeService.generateQRCode(text).then(qr => {
      this.qrCodeDataUrl = qr;
    });
  }
}
