import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AccountConstant, SettingsData } from 'src/app/entities/account-control';
import { TransactionFromGoogle } from 'src/app/services/google-services/google.service';
import { AccountControlManagementCategoriesComponent } from '../account-control-management-categories/account-control-management-categories.component';
import { AccountControlManagementAccountComponent } from '../account-control-management-account/account-control-management-account.component';
import { AccountControlManagementTransactionComponent } from '../account-control-management-transaction/account-control-management-transaction.component';

@Component({
  selector: 'app-account-control-list-transactions',
  templateUrl: './account-control-list-transactions.component.html',
  styleUrls: ['./account-control-list-transactions.component.css']
})
export class AccountControlListTransactionsComponent implements OnInit {

  visibleTransactions: TransactionFromGoogle[] = []; // Transacciones visibles en la vista
  filteredTransactions: TransactionFromGoogle[] = []; // Transacciones filtradas con el input de bsqueda
  pageSize = 10; // Nmero de transacciones por pgina
  currentPage = 0; // Pgina actual

  totalIncome = 0;
  totalExpense = 0;
  savingsGoal = 0;
  isFiltering = false; // Variable para verificar si se est filtrando


  private _transactions: TransactionFromGoogle[];
  @Input()
  set transactions(value: TransactionFromGoogle[]) {
    if (value) {
      this.onSettionsDataChange(value); // Llama a un mtodo cuando el valor cambia
    }
  }

  onSettionsDataChange(value: TransactionFromGoogle[]) {
    this._transactions = JSON.parse(JSON.stringify(value));
    if (value && value.length > 0) {
      this.ngOnInit();
    }
  }

  get transactions(): TransactionFromGoogle[] {
    return this._transactions;
  }

  @Input()
  settionsData: SettingsData;


  @Output()
  onFinish = new EventEmitter<void>();

  constructor(private modalService: NgbModal) { }

  ngOnInit(): void {
    this.filteredTransactions = [];
    this.visibleTransactions = [];
    this.currentPage = 0;
    this.resetMetrics();
    this.filteredTransactions = [...this.transactions]; // Inicialmente sin filtrar
    this.loadMoreTransactions(); // Carga los primeros 30 registros
  }

  onScroll(event: any): void {
    const element = event.target;
    // Cambia a <= para asegurarte de activar el evento cerca del final
    if (element.scrollHeight - element.scrollTop <= element.clientHeight + 1) {
      this.loadMoreTransactions();
    }
  }


  onSearch(event: Event): void {
    const input = (event.target as HTMLInputElement).value;
    this.isFiltering = input.length > 0;

    if (this.isFiltering) {
      this.filteredTransactions = this.transactions.filter(transaction =>
        JSON.stringify(transaction).toLowerCase().includes(input.toLowerCase())
      );
      this.calculateMetrics(this.filteredTransactions); // Actualiza mtricas
      this.visibleTransactions = this.filteredTransactions.slice(0, 30); // Primera pgina de resultados
    } else {
      this.filteredTransactions = [...this.transactions];
      this.visibleTransactions = this.transactions.slice(0, 30);
      this.resetMetrics(); // Restaura mtricas si no hay filtro
    }
  }

  calculateMetrics(transactions: any[]): void {
    this.totalIncome = transactions
      .filter(transaction => transaction.type === AccountConstant.TRANSACTION_TYPE_INCOME)
      .reduce((sum, transaction) => sum + transaction.amount, 0);
    this.totalExpense = transactions
      .filter(transaction => transaction.type === AccountConstant.TRANSACTION_TYPE_EXPENSE)
      .reduce((sum, transaction) => sum + transaction.amount, 0);
    this.savingsGoal = this.totalIncome - Math.abs(this.totalExpense);
  }

  resetMetrics(): void {
    this.totalIncome = 0;
    this.totalExpense = 0;
    this.savingsGoal = 0;
  }

  // Carga la siguiente pgina de transacciones
  loadMoreTransactions(): void {
    const startIndex = this.currentPage * this.pageSize;
    const endIndex = startIndex + this.pageSize;
    const newTransactions = this.filteredTransactions.slice(startIndex, endIndex);
    this.visibleTransactions = [...this.visibleTransactions, ...newTransactions];
    this.currentPage++;
  }

  openManagementCategories(transaction?: TransactionFromGoogle) {
    const componentRef = this.modalService.open(AccountControlManagementCategoriesComponent, { size: 'xxl' });
    const componentInstance = componentRef.componentInstance as AccountControlManagementCategoriesComponent;
    componentInstance.transaction = transaction;
    componentInstance.settionsData = this.settionsData;
    componentInstance.onFinish.subscribe((data) => {
      this.onFinish.emit();
      componentRef.close();
    });
  }

  openCreateAccount(transaction?: TransactionFromGoogle) {
    const componentRef = this.modalService.open(AccountControlManagementAccountComponent, { size: 'xl' });
    const componentInstance = componentRef.componentInstance as AccountControlManagementAccountComponent;
    componentInstance.transaction = transaction;
    componentInstance.onFinish.subscribe((data) => {
      this.onFinish.emit();
      componentRef.close();
    });
  }

  openEditTransaction(transaction?: TransactionFromGoogle) {
    const componentRef = this.modalService.open(AccountControlManagementTransactionComponent, { size: 'xl' });
    const componentInstance = componentRef.componentInstance as AccountControlManagementTransactionComponent;
    componentInstance.transaction = transaction;
    componentInstance.settingsData = this.settionsData;
    componentInstance.onTransactionSave.subscribe((data) => {
      this.onFinish.emit();
      componentRef.close();
    });
  }
}
