import { Injectable } from '@angular/core';
import { gapi } from 'gapi-script';
import { ToastrService } from 'ngx-toastr';
import { BehaviorSubject } from 'rxjs';
import { SettingsData, Transaction, AccountConstant } from 'src/app/entities/account-control';
import { Configuracion } from 'src/app/entities/business-control';
import { TransactionOperation } from '../utils/transaction-operation';

@Injectable({
  providedIn: 'root'
})
export class GoogleService {
  static isGettingEmail = false;
  static gettingEmailPromise: Promise<TransactionFromGoogle[]> = null;
  private CLIENT_ID = '746365364970-d423bo7481d242h1hrbs31p2q3tbsusd.apps.googleusercontent.com';
  private API_KEY = 'TU_API_KEY'; // Opcional si solo usas OAuth
  private SCOPES = "https://www.googleapis.com/auth/drive.file https://www.googleapis.com/auth/gmail.readonly";
  private DISCOVERY_DOCS = [
    "https://www.googleapis.com/discovery/v1/apis/drive/v3/rest",
    "https://www.googleapis.com/discovery/v1/apis/gmail/v1/rest"
  ];

  private get FOLDER_NAME(): string {
    if (window.location.hostname == 'localhost')
      return 'business-control-test';
    return 'business-control';
  };

  private get FOLDER_NAME_ACCOUNT_CONTROL(): string {
    if (window.location.hostname == 'localhost')
      return 'account-control-test';
    return 'account-control';
  };

  private config: Configuracion;
  private configData: SettingsData;
  private countGetConfig: number = 0;
  private countGetSettingData: number = 0;


  constructor(private toastr: ToastrService) {
    this.loadGapiClient();
  }


  signOut() {
    gapi.auth2.getAuthInstance().signOut();
  }

  private gapiLoaded$ = new BehaviorSubject<boolean>(false);


  private loadGapiClient(): Promise<void> {
    try {
      return new Promise((resolve, reject) => {
        gapi.load('client:auth2', async () => {
          await gapi.client.load('gmail', 'v1');
          gapi.client.init({
            // apiKey: this.API_KEY,
            clientId: this.CLIENT_ID,
            discoveryDocs: this.DISCOVERY_DOCS,
            scope: this.SCOPES
          }).then(() => {
            this.gapiLoaded$.next(true);
            resolve();
          }).catch((error) => {
            this.toastr.error("Error al inicializar Google Drive" + JSON.stringify(error), 'Business Control!');
            this.gapiLoaded$.next(false);
            reject(error);
          });
        });
      });
    } catch (error) {
      this.toastr.error("Control de error al inicializar Google Drive: " + JSON.stringify(error), 'Business Control!');
    }
  }

  async signIn(): Promise<UserLoggedWithData | undefined> {
    try {
      if (!this.gapiLoaded$.getValue()) {
        await this.loadGapiClient();
      }
      if (!this.gapiLoaded$.getValue()) {
        return undefined;
      }
      const user = await gapi.auth2.getAuthInstance().signIn();
      await this.getConfigFromUser();
      return {
        data: this.config,
        user: {
          email: user.getBasicProfile().getEmail(),
          username: user.getBasicProfile().getName(),
        }
      };


    } catch (error) {
      this.toastr.error("Control de error en login: " + JSON.stringify(error), 'Business Control!');
    }
  }

  async signInForAccountControl(): Promise<UserLoggedWithSettingsData | undefined> {
    try {
      if (!this.gapiLoaded$.getValue()) {
        await this.loadGapiClient();
      }
      if (!this.gapiLoaded$.getValue()) {
        return undefined;
      }
      const user = await gapi.auth2.getAuthInstance().signIn();
      await this.getSettingDataFromUser();
      return {
        data: this.configData,
        user: {
          email: user.getBasicProfile().getEmail(),
          username: user.getBasicProfile().getName(),
        }
      };

    } catch (error) {
      this.toastr.error("Control de error en login: " + JSON.stringify(error), 'Business Control!');
    }
  }

  private async getConfigFromUser() {
    if (!this.config && this.countGetConfig == 0) {
      const data = await this.downloadFirstJsonFileFromBusinessControlFolder(this.FOLDER_NAME);
      this.config = data ? JSON.parse(data as string) : undefined;
      if (this.config) {
        if (this.config.ultimaModificacion) {
          this.config.ultimaModificacion = new Date(this.config.ultimaModificacion);
        }
      }
    }
    this.countGetConfig++;
  }

  private async getSettingDataFromUser() {
    console.log('getSettingDataFromUser', this.countGetSettingData);
    if (!this.configData && this.countGetSettingData == 0) {
      const data = await this.downloadFirstJsonFileFromBusinessControlFolder(this.FOLDER_NAME_ACCOUNT_CONTROL);
      this.configData = data ? JSON.parse(data as string) : undefined;
      if (this.configData) {
        if (this.configData.lastSync) {
          this.configData.lastSync = new Date(this.configData.lastSync);
        }
      }
    }
    this.countGetSettingData++;
  }

  async getUser(): Promise<UserLoggedWithData | undefined> {
    if (!this.gapiLoaded$.getValue()) {
      await this.loadGapiClient();
    }
    if (this.gapiLoaded$.getValue()) {
      const userGoogle = gapi.auth2.getAuthInstance().currentUser.get();
      if (userGoogle.isSignedIn()) {
        await this.getConfigFromUser();
        return {
          data: this.config,
          user: {
            email: userGoogle.getBasicProfile().getEmail(),
            username: userGoogle.getBasicProfile().getName(),
          }
        };
      }
    }
    return undefined;
  }

  async getUserForAccountControl(): Promise<UserLoggedWithSettingsData | undefined> {
    if (!this.gapiLoaded$.getValue()) {
      await this.loadGapiClient();
    }
    if (this.gapiLoaded$.getValue()) {
      const userGoogle = gapi.auth2.getAuthInstance().currentUser.get();
      if (userGoogle.isSignedIn()) {
        await this.getSettingDataFromUser();
        return {
          data: this.configData,
          user: {
            email: userGoogle.getBasicProfile().getEmail(),
            username: userGoogle.getBasicProfile().getName(),
          }
        };
      }
    }
    return undefined;
  }

  async isUserLogged(): Promise<boolean> {
    if (!this.gapiLoaded$.getValue()) {
      await this.loadGapiClient();
    }
    if (!this.gapiLoaded$.getValue()) {
      return false;
    }
    const userGoogle = gapi.auth2.getAuthInstance().currentUser.get();
    return userGoogle.isSignedIn();
  }

  private uploadBlobToFolder(blob: Blob, fileName: string, folderId: string, accessToken: string): Promise<any> {
    const metadata = {
      'name': fileName,
      'mimeType': blob.type,
      'parents': [folderId]  // Subir el archivo a la carpeta especificada
    };

    const formData = new FormData();
    formData.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
    formData.append('file', blob);

    return fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${accessToken}`
      },
      body: formData
    })
      .then(response => response.json())
      .then(data => {
        this.toastr.success('Informacin creada exitosamente:' + JSON.stringify(data), 'Business Control!');
      })
      .catch(error => {
        this.toastr.error('Error al subir el archivo:' + JSON.stringify(error), 'Business Control!');
      });
  }


  private findFileInFolder(fileName: string, folderId: string, accessToken: string): Promise<string | null> {
    return fetch(`https://www.googleapis.com/drive/v3/files?q='${folderId}' in parents and name='${fileName}' and trashed=false`, {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${accessToken}`,
        'Accept': 'application/json'
      }
    })
      .then(response => response.json())
      .then(data => {
        const files = data.files;
        if (files && files.length > 0) {
          return files[0].id;  // Retorna el ID del archivo si existe
        }
        return null;  // Retorna null si no encuentra el archivo
      });
  }


  private updateFile(fileId: string, blob: Blob, accessToken: string): Promise<any> {
    const metadata = {
      'mimeType': blob.type
    };

    const formData = new FormData();
    formData.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
    formData.append('file', blob);

    return fetch(`https://www.googleapis.com/upload/drive/v3/files/${fileId}?uploadType=multipart`, {
      method: 'PATCH',  // Utilizamos PATCH para actualizar el archivo
      headers: {
        'Authorization': `Bearer ${accessToken}`
      },
      body: formData
    })
      .then(response => response.json())
      .then(data => {
        this.toastr.success('Informacin actualizada exitosamente:' + JSON.stringify(data), 'Business Control!');
      })
      .catch(error => {
        this.toastr.error('Error al actualizar el archivo:' + JSON.stringify(error), 'Business Control!');
      });
  }


  async uploadFileWithFolderDetection(blob: Blob, filename: string, isAccountControl: boolean = false) {
    const folderName: string = !isAccountControl ? this.FOLDER_NAME : this.FOLDER_NAME_ACCOUNT_CONTROL;
    const accessToken = gapi.auth2.getAuthInstance().currentUser.get().getAuthResponse().access_token;

    await this.findFolderByName(folderName, accessToken)
      .then(folderId => {
        if (folderId) {
          this.toastr.info('Carpeta encontrada, ID:' + folderId, 'Business Control!');

          // Verificamos si el archivo ya existe en la carpeta
          return this.findFileInFolder(filename, folderId, accessToken)
            .then(fileId => {
              if (fileId) {
                // El archivo ya existe, lo actualizamos
                this.toastr.info('Archivo encontrado, actualizando...', 'Business Control!');
                return this.updateFile(fileId, blob, accessToken);
              } else {
                // El archivo no existe, lo subimos
                this.toastr.warning('Archivo no encontrado, subiendo nuevo archivo...', 'Business Control!');
                return this.uploadBlobToFolder(blob, filename, folderId, accessToken);
              }
            });
        } else {
          // La carpeta no existe, la creamos
          this.toastr.warning('Carpeta no encontrada, creando nueva carpeta...', 'Business Control!');
          return this.createFolder(folderName, accessToken)
            .then(newFolderId => {
              this.toastr.info('Nueva carpeta creada, ID:' + newFolderId, 'Business Control!');
              return this.uploadBlobToFolder(blob, filename, newFolderId, accessToken);
            });
        }
      })
      .catch(error => {
        this.toastr.error('Error al manejar el archivo:' + JSON.stringify(error), 'Business Control!');
      });
  }


  private findFolderByName(folderName: string, accessToken: string): Promise<string | null> {
    return fetch(`https://www.googleapis.com/drive/v3/files?q=mimeType='application/vnd.google-apps.folder' and name='${folderName}' and trashed=false`, {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${accessToken}`,
        'Accept': 'application/json'
      }
    })
      .then(response => response.json())
      .then(data => {
        const files = data.files;
        if (files && files.length > 0) {
          return files[0].id;  // Retorna el ID de la carpeta si existe
        }
        return null;  // Retorna null si no encuentra la carpeta
      });
  }


  private createFolder(folderName: string, accessToken: string): Promise<string> {
    const metadata = {
      'name': folderName,
      'mimeType': 'application/vnd.google-apps.folder'
    };

    return fetch('https://www.googleapis.com/drive/v3/files', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${accessToken}`,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(metadata)
    })
      .then(response => response.json())
      .then(data => data.id);  // Retorna el ID de la carpeta creada
  }

  private async downloadFirstJsonFileFromBusinessControlFolder(folderName: string): Promise<string | null> {
    const accessToken = gapi.auth2.getAuthInstance().currentUser.get().getAuthResponse().access_token;

    try {
      // Buscar la carpeta por nombre
      const folderId = await this.findFolderByName(folderName, accessToken);
      if (!folderId) {
        this.toastr.warning('Carpeta no encontrada', 'Business Control!');
        return null;
      }

      // Buscar el primer archivo .json en la carpeta
      const fileId = await this.findFirstJsonFileInFolder(folderId, accessToken);
      if (!fileId) {
        this.toastr.warning('Archivo .json no encontrado en la carpeta', 'Business Control!');
        return null;
      }

      // Descargar el contenido del archivo
      return this.downloadFileContent(fileId, accessToken);
    } catch (error) {
      this.toastr.info('Error al descargar el archivo:' + JSON.stringify(error), 'Business Control!');
      return null;
    }
  }

  private findFirstJsonFileInFolder(folderId: string, accessToken: string): Promise<string | null> {
    return fetch(`https://www.googleapis.com/drive/v3/files?q='${folderId}' in parents and trashed=false`, {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${accessToken}`,
        'Accept': 'application/json'
      }
    })
      .then(response => response.json())
      .then(data => {
        const files = data.files;
        if (files && files.length > 0) {
          // Filtrar archivos con extensin .json
          const jsonFiles = files.filter(file => file.name.endsWith('.json'));
          if (jsonFiles.length > 0) {
            return jsonFiles[0].id;  // Retorna el ID del primer archivo .json
          }
        }
        return null;  // Retorna null si no encuentra ningn archivo .json
      });
  }


  private downloadFileContent(fileId: string, accessToken: string): Promise<string> {
    return fetch(`https://www.googleapis.com/drive/v3/files/${fileId}?alt=media`, {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${accessToken}`
      }
    })
      .then(response => response.text());  // Retorna el contenido del archivo como string
  }

  // Obtener correos electrnicos nuevos a partir de una fecha dada y de varios remitentes
  async getNewEmailsSince(date: Date, senders: string[] = ['alertasynotificaciones@notificacionesbancolombia.com', 'alertasynotificaciones@bancolombia.com.co']): Promise<any[]> {
    console.log("Consultando emails", date, senders);
    const accessToken = gapi.auth2.getAuthInstance().currentUser.get().getAuthResponse().access_token;

    // Convertir la fecha a formato RFC 3339
    const rfc3339Date = date.toISOString();
    const gmail: any = gapi.client['gmail'];

    // Crear una promesa para cada remitente en el arreglo
    const promises = senders.map(sender => {
      const query = `from:${sender} after:${Math.floor(new Date(rfc3339Date).getTime() / 1000)}`;
      return gmail.users.messages.list({
        'userId': 'me',
        'q': query,
      });
    });

    try {
      // Ejecutar todas las promesas en paralelo y esperar los resultados
      const responses = await Promise.all(promises);

      // Extraer y combinar los IDs de los correos electrnicos de cada respuesta sin flatMap
      const emailIds = responses.reduce((acc: any[], response) => {
        const messages = response.result.messages || [];
        return acc.concat(messages.map((message: any) => message.id));
      }, []);

      // Obtener detalles de todos los correos electrnicos combinados
      if (emailIds.length > 0) {
        return await this.fetchEmailDetails(emailIds);
      } else {
        this.toastr.info('No se encontraron correos electrnicos nuevos desde la fecha especificada.', 'Business Control!');
        return [];
      }
    } catch (error) {
      this.toastr.info('Error al consultar los correos electrnicos:' + JSON.stringify(error), 'Business Control!');
      return [];
    }
  }



  // Obtener detalles de cada correo a partir de sus IDs
  private async fetchEmailDetails(emailIds: string[]): Promise<any[]> {
    const emails = [];
    const gmail: any = gapi.client['gmail'];

    for (const emailId of emailIds) {
      const email = await gmail.users.messages.get({
        'userId': 'me',
        'id': emailId
      });

      emails.push(email.result);
    }

    return emails;
  }

  // Procesar correos electrnicos para extraer transacciones
  processEmailsForTransactions(emails: any[]): TransactionFromGoogle[] {
    const transactions: TransactionFromGoogle[] = [];
    const helper = new TransactionOperation();
    emails.forEach(email => {
      const body = this.getEmailBody(email);
      const idEmail = this.getIdEmail(email);

      // Buscar patrones para detectar compras, transferencias e ingresos
      const transaction = helper.getTransaction(body, idEmail);
      transactions.push(transaction);
    });

    return transactions;
  }

  private getIdEmail(email: any): string {
    return email.id;
  }

  // Obtener el cuerpo del correo electrnico
  private getEmailBody(email: any): string {
    return email.snippet;
    // const bodyData = email.payload.parts.filter((part: any) => part.mimeType === 'text/html' || part.mimeType === 'text/plain')[0].body.data;
    // return atob(bodyData.replace(/-/g, '+').replace(/_/g, '/'));
  }


  async getTransactionsSince(date: Date): Promise<TransactionFromGoogle[]> {
    try {
      console.log("getTransactionsSince", date);
      if (!GoogleService.isGettingEmail)
        GoogleService.gettingEmailPromise = this.getNewEmailsSince(date);;
      GoogleService.isGettingEmail = true;
      const emails = await GoogleService.gettingEmailPromise;
      GoogleService.isGettingEmail = false;
      if (emails.length === 0) {
        return [];
      }

      const transactions = this.processEmailsForTransactions(emails);
      if (transactions.length === 0) {
        this.toastr.warning('No se encontraron transacciones en los correos.', 'Business Control!');
      }
      return transactions;

    } catch (error) {
      console.error(error);
      this.toastr.error('Error al obtener transacciones: ' + JSON.stringify(error), 'Business Control!');
      return [];
    }
  }

}

