import { Injectable } from '@angular/core';
import { Bien, Comprobante } from 'src/app/entities/business-control';
import { BusinessControlService } from '../business-control/business-control.service';

export class Amortizacion {
  index: number;
  cuota: number;
  interes: number;
  tarifaFija: number;
  abonoCapital: number;
  abonoAdicionalCapital: number;
  soloInteres: boolean;
  saldoRestante: number;
  fechaCuota: Date; // Nuevo campo para la fecha de la cuota
  comprobante?: Comprobante;
  // Getter para obtener el total
  get total(): number {
    return this.interes + this.tarifaFija + this.abonoCapital + this.abonoAdicionalCapital;
  }
}


export interface ResponseAmortizacion {
  amortizaciones: Amortizacion[];
  ahorroIntereses: number;
  plazoReducido: number;
}

@Injectable({
  providedIn: 'root'
})
export class CreditService {
  constructor() { }

  private redondear(valor: number): number {
    return Math.round(valor);
  }

  private sumarMeses(fecha: Date, meses: number): Date {
    const nuevaFecha = new Date(fecha);
    nuevaFecha.setMonth(nuevaFecha.getMonth() + meses);
    return nuevaFecha;
  }

  private recalcularTablaAmortizacion(
    saldoInicial: number,
    tasaMensual: number,
    numCuotas: number,
    fechaInicio: Date,// Nuevo parmetro para la fecha de inicio
    tarifaFija: number = 0,
    soloInteres: Record<number, { comprobante: Comprobante, valorInteres: number }> = {},
    abonosCapital: Record<number, { comprobante: Comprobante, valorAbono: number }> = {},
  ): Amortizacion[] {
    let tablaAmortizacion: Amortizacion[] = [];
    let cuotaFija;

    cuotaFija = this.calcularCuotaFija(tasaMensual, saldoInicial, numCuotas, tarifaFija);

    // Crear la tabla de amortizacin
    let saldo = saldoInicial;

    // Generar la tabla de amortizacin
    for (let index = 1; index <= numCuotas; index++) {
      let interes = 0;
      let abonoCapital = 0;
      let cuotaFijaTemp = cuotaFija;

      // Verificar si hay un abono adicional para este mes
      let comprobanteCapital = abonosCapital[index] || null;
      let comprobanteIntereses = soloInteres[index] || null;
      let abonoAdicionalCapital = comprobanteCapital?.valorAbono ?? 0;
      let interesesComprobante = comprobanteIntereses?.valorInteres ?? 0;
      let comprobante = comprobanteCapital?.comprobante ?? comprobanteIntereses?.comprobante ?? null

      if (tasaMensual === 0) {
        // Crdito sin inters
        abonoCapital = this.redondear(cuotaFija);
      } else {
        // Crdito con inters
        interes = this.redondear(saldo * tasaMensual);  // Calcular inters redondeado
        abonoCapital = this.redondear(cuotaFija - interes);  // Cuota menos inters y seguro
      }
      let cuotaSoloInteres = interesesComprobante == interes && interes > 0;

      if (cuotaSoloInteres) {
        abonoCapital = 0;
        cuotaFijaTemp = 0;
        cuotaFija = this.calcularCuotaFija(tasaMensual, saldo, numCuotas - index, tarifaFija);
      }

      // Verificar si estamos en la ltima cuota o Si el saldo es menor que la cuota fija antes de la ltima cuota, ajusta la cuota a igualar al saldo
      if (abonoCapital > saldo || index === numCuotas) {
        abonoCapital = saldo; // Ajusta abono capital al saldo restante
      }

      saldo = this.redondear(saldo - abonoCapital - abonoAdicionalCapital);  // Actualizar el saldo restante

      const amortizacion = new Amortizacion();
      amortizacion.index = index;
      amortizacion.cuota = cuotaFijaTemp;
      amortizacion.interes = interes;
      amortizacion.tarifaFija = tarifaFija;
      amortizacion.abonoCapital = abonoCapital;
      amortizacion.saldoRestante = saldo;
      amortizacion.comprobante = comprobante;
      amortizacion.abonoAdicionalCapital = abonoAdicionalCapital;
      amortizacion.soloInteres = cuotaSoloInteres;

      // Asignar la fecha correspondiente sumando meses a la fecha de inicio
      amortizacion.fechaCuota = this.sumarMeses(fechaInicio, index - 1);

      // Agregar los valores a la tabla
      tablaAmortizacion.push(amortizacion);

      // Si el saldo se vuelve 0 antes del final, no es necesario continuar
      if (saldo <= 0) {
        break;
      }
    }

    return tablaAmortizacion;
  }

  public calcularAmortizacion(bien: Bien): ResponseAmortizacion {
    let amortizaciones = this.recalcularTablaAmortizacion(
      bien.contratoActivo.valorCredito,
      bien.contratoActivo.tasaEM / 100,
      bien.contratoActivo.plazoMeses,
      bien.contratoActivo.inicio,
      bien.contratoActivo.tarifaFija,
    )
    const totalIntereses = this.totalIntereses(amortizaciones);
    let abonosCapital: Record<number, { comprobante: Comprobante, valorAbono: number }> = {};
    let soloIntereses: Record<number, { comprobante: Comprobante, valorInteres: number }> = {};
    amortizaciones.forEach((am, i) => {
      bien.comprobantes.forEach((com, j) => {
        if (am.fechaCuota.toLocaleDateString('en-CA').replace(/-/g, '') == com.periodo.inicio.toLocaleDateString('en-CA').replace(/-/g, '')) {
          const conceptoAbono = com.conceptos.find(t => t.descripcion == BusinessControlService.CONCEPTO_ABONO_CAPITAL);
          if (conceptoAbono) {
            abonosCapital[i + 1] = {
              comprobante: com,
              valorAbono: conceptoAbono.valor
            };
          }
          const conceptoIntereses = com.conceptos.find(t => t.descripcion == BusinessControlService.CONCEPTO_INTERESES);
          if (conceptoIntereses && com.conceptos.length == 1) {
            soloIntereses[i + 1] = {
              comprobante: com,
              valorInteres: conceptoIntereses.valor
            };
          }
        }
      });
    });
    amortizaciones = this.recalcularTablaAmortizacion(
      bien.contratoActivo.valorCredito,
      bien.contratoActivo.tasaEM / 100,
      bien.contratoActivo.plazoMeses,
      bien.contratoActivo.inicio,
      bien.contratoActivo.tarifaFija,
      soloIntereses,
      abonosCapital
    );
    let ahorroIntereses = 0;
    if (amortizaciones.length < bien.contratoActivo.plazoMeses) {
      ahorroIntereses = this.totalIntereses(amortizaciones);
      ahorroIntereses = totalIntereses - ahorroIntereses;
    }
    return {
      amortizaciones: amortizaciones,
      ahorroIntereses: ahorroIntereses,
      plazoReducido: bien?.contratoActivo?.plazoMeses - amortizaciones.length
    }
  }

  private totalIntereses(amortizaciones: Amortizacion[]) {
    let ahorroIntereses: number = 0;
    amortizaciones.forEach(t => {
      ahorroIntereses += t.interes;
    });
    return ahorroIntereses;
  }

  private calcularCuotaFija(tasaMensual: number, saldoInicial: number, numCuotas: number, tarifaFija: number) {
    let cuotaFija: number;
    if (tasaMensual === 0) {
      // Crdito sin inters
      cuotaFija = this.redondear(saldoInicial / numCuotas) + tarifaFija;
    } else {
      // Crdito con inters
      let cuotaMensualSinSeguro = this.redondear((saldoInicial * tasaMensual) / (1 - Math.pow(1 + tasaMensual, -numCuotas)));
      cuotaFija = this.redondear(cuotaMensualSinSeguro);
    }
    return cuotaFija;
  }
}
