import { Injectable } from '@angular/core';
import * as QRCode from 'qrcode';

@Injectable({
  providedIn: 'root'
})
export class QrCodeService {

  constructor() { }

  // Mtodo para generar un cdigo QR en formato de Data URL
  public generateQRCode(text: string): Promise<string> {
    return QRCode.toDataURL(text, {
      errorCorrectionLevel: 'H', // Nivel de correccin de errores: L, M, Q, H
      type: 'image/png',
      width: 400, // Ancho del cdigo QR
      margin: 2 // Margen alrededor del cdigo QR
    });
  }

  // Mtodo para generar un cdigo QR en formato de imagen SVG
  public generateQRCodeSVG(text: string): Promise<string> {
    return QRCode.toString(text, {
      type: 'svg',
      errorCorrectionLevel: 'H'
    });
  }
}
