import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Account } from 'src/app/entities/account-control';
import { AccountControlService } from 'src/app/services/account-control/account-control.service';
import { TransactionFromGoogle } from 'src/app/services/google-services/google.service';

@Component({
  selector: 'app-account-control-management-account',
  templateUrl: './account-control-management-account.component.html',
  styleUrls: ['./account-control-management-account.component.css']
})
export class AccountControlManagementAccountComponent implements OnInit {
  configuracionForm: FormGroup;

  private _transaction: TransactionFromGoogle;

  @Input()
  set transaction(value: TransactionFromGoogle) {
    this._transaction = value;
    this.onTransactionChange(value); // Llama a un mtodo cuando el valor cambia
  }

  get transaction(): TransactionFromGoogle {
    return this._transaction;
  }

  public disableAccountId = false;

  private _accountEdit: Account;

  @Input()
  set accountToEdit(value: Account) {
    this._accountEdit = value;
    this.onAccountChange(value); // Llama a un mtodo cuando el valor cambia
  }

  get accountToEdit(): Account {
    return this._accountEdit;
  }

  @Output()
  onFinish = new EventEmitter<Account>();

  constructor(private fb: FormBuilder, private accountService: AccountControlService) {
    this.configuracionForm = this.fb.group({
      accountId: ['', Validators.required],
      accountName: ['', Validators.required],
      accountDescription: ['', Validators.required],
      accountBalance: [0, Validators.required],
      accountIsPrincipal: [false],
      submitted: [false]
    });
  }

  onAccountChange(value: Account) {
    if (value) {
      this.disableAccountId = true;
      this.configuracionForm.patchValue({ accountId: value.id });
      this.configuracionForm.patchValue({ accountName: value.name });
      this.configuracionForm.patchValue({ accountDescription: value.description });
      this.configuracionForm.patchValue({ accountBalance: value.balance });
      this.configuracionForm.patchValue({ accountIsPrincipal: value.isPrincipal });
    }
  }

  onTransactionChange(value: TransactionFromGoogle) {
    if (value) {
      this._transaction.originalAccount = this._transaction.originalAccount.replace("*", "");
      this.configuracionForm.patchValue({ accountId: value.originalAccount });
    }
  }

  ngOnInit(): void {
  }

  onSubmitInitial(): void {
    this.configuracionForm.patchValue({ submitted: true });
    if (this.configuracionForm.valid) {
      const formValues = this.configuracionForm.value;
      const account = {
        id: formValues.accountId,
        name: formValues.accountName,
        description: formValues.accountDescription,
        balance: formValues.accountBalance,
        transactions: [],
        lastBalance: 0,
        balanceLastMonth: 0,
        lastTransaction: null,
        isPrincipal: formValues.accountIsPrincipal
      };
      this.accountService.saveAccount(account);
      this.onFinish.emit(account);
    }
  }
}
