import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Comprobante } from 'src/app/entities/business-control';
import { CryptService } from 'src/app/services/crypt/crypt.service';

@Component({
  selector: 'app-business-control-verify',
  templateUrl: './business-control-verify.component.html',
  styleUrls: ['./business-control-verify.component.css']
})
export class BusinessControlVerifyComponent implements OnInit {
  comprobante: Comprobante;

  constructor(private cryptService: CryptService, private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    // Obtener el valor del query param "comprobante"
    this.route.queryParamMap.subscribe(params => {
      try {
        const comprobanteCrypted = params.get('comprobante');
        const aux = this.cryptService.decrypt(comprobanteCrypted);
        this.comprobante = JSON.parse(aux);
        this.comprobante.fechaGeneracion = new Date(this.comprobante.fechaGeneracion);
        if (!this.comprobante.contratante) {
          this.comprobante.bien.comprobantes.forEach(t => {
            if (t && t.contratante && t.contratoId == this.comprobante.contratoId) {
              this.comprobante.contratante = t.contratante;
              return;
            }
          })
        }
      } catch (error) {
        console.log(error);
      }
    });
  }
}
