import { Injectable } from '@angular/core';
import { Contrato, Comprobante, Configuracion, ReciboServicio, Bien, ContratoID, Contratante } from 'src/app/entities/business-control';
import { CryptService } from '../crypt/crypt.service';
import { HttpClient } from '@angular/common/http';
import { GoogleService } from '../google-services/google.service';
import { config } from 'process';
import { ToastrService } from 'ngx-toastr';

@Injectable({
  providedIn: 'root'
})
export class BusinessControlService {
  public get storageKey(): string {
    if (window.location.hostname == 'localhost')
      return 'configuracionLocal';
    return 'configuracion';
  }
  private storageSelectedBienKey = 'selectedBienId';
  static isSync = false;
  static readonly TIPO_BIEN_CREDITO = 'Credito';
  static readonly TIPO_BIEN_ARRIENDO = 'Arrendamiento';

  static readonly CONCEPTO_ABONO_CAPITAL = 'Abono capital';
  static readonly CONCEPTO_INTERESES = 'Interses';
  static readonly CONCEPTO_CAPITAL = 'Capital';
  static readonly CONCEPTO_TARIFA_FIJA = 'Tarifa fija';
  static readonly CONCEPTO_OTRO = 'Otro';

  static readonly CONCEPTO_MENSUALIDAD = 'Mensualidad';
  static readonly CONCEPTO_DEPOSITO = 'Depsito';
  static readonly CONCEPTO_DEPOSITO_50 = 'Depsito (50%)';


  constructor(private toastr: ToastrService, private googleService: GoogleService, private cryptService: CryptService, private _httpService: HttpClient) { }

  getConfiguracion(): Configuracion | null {
    const data = this._getConfigFromLocalStorage();
    if (data) {
      data.bienes = data.bienes as Bien[];
      data.bienes.forEach(bien => {
        let totalIngresos = 0;
        let totalIngrByArrendaMap = new Map();
        let countComprByArrendaMap = new Map();
        if (!bien.comprobantes) bien.comprobantes = [];
        if (!bien.serviciosPublicos) bien.serviciosPublicos = [];
        if (!bien.contratos) bien.contratos = [];
        bien?.comprobantes?.forEach(comprobante => {
          const tempTotalIdContrato = totalIngrByArrendaMap.get(comprobante.contratoId) ?? 0;
          const tempCountIdContrato = countComprByArrendaMap.get(comprobante.contratoId) ?? 0;
          totalIngrByArrendaMap.set(comprobante.contratoId, tempTotalIdContrato as number + comprobante.total);
          countComprByArrendaMap.set(comprobante.contratoId, tempCountIdContrato as number + 1);
          comprobante.fechaGeneracion = new Date(comprobante.fechaGeneracion);
          comprobante.periodo.inicio = new Date(comprobante.periodo.inicio);
          comprobante.periodo.fin = new Date(comprobante.periodo.fin);
          totalIngresos += comprobante.total;
          comprobante.contratante = data.contratante;
          comprobante.bien = bien;
        });
        bien?.serviciosPublicos?.forEach(servicio => {
          servicio.fechaGeneracion = new Date(servicio.fechaGeneracion);
          servicio.fechaPago = servicio.fechaPago ? new Date(servicio.fechaPago) : null;
        });
        bien.totalIngresos = totalIngresos;
        bien?.contratos?.forEach(contrato => {
          contrato.inicio = new Date(contrato.inicio);
          contrato.totalIngresos = totalIngrByArrendaMap.get(contrato.id) as number ?? 0;
          contrato.porcentajeIngresos = bien.totalIngresos > 0 ? Math.round(contrato.totalIngresos * 100 / bien.totalIngresos) : 0;
          contrato.comprobantesGenerados = countComprByArrendaMap.get(contrato.id) as number ?? 0
          if (contrato.activo) {
            bien.contratoActivo = contrato;
          }
        });
        bien.contratos.sort((a, b) => a.activo > b.activo ? -1 : 1);
        bien.comprobantes.sort((a, b) => a.fechaGeneracion > b.fechaGeneracion ? -1 : 1);
        if (data.ultimaModificacion) {
          data.ultimaModificacion = new Date(data.ultimaModificacion);
        } else {
          data.ultimaModificacion = new Date();
        }
      });
    }
    this.syncWithGoogleDrive(data);
    return data;
  }

  public setSelectedId(bienId: string): void {
    localStorage.setItem(this.storageSelectedBienKey, bienId);
  }

  public getSelectedId(): string {
    return localStorage.getItem(this.storageSelectedBienKey);
  }

  private async syncWithGoogleDrive(configLocalStorage: Configuracion) {
    if (!BusinessControlService.isSync || BusinessControlService.isSync && !configLocalStorage) {
      BusinessControlService.isSync = true;
      const userLogged = await this.googleService.getUser();
      if (userLogged && userLogged.data) {
        let uploadConfigInGoogle = false;
        if (userLogged.data && !userLogged.data.ultimaModificacion) {
          userLogged.data.ultimaModificacion = new Date();
          //se manda a subir la data nuevamente a google drive ya que en google drive no hay fecha de modificacion en el json
          uploadConfigInGoogle = true;
        }
        //existe data en google y no hay fecha de modificacion o la fecha de modificacion de localstorage es menor a la de google entonces bajo y almaceno los datos en local
        if (userLogged.data && (!configLocalStorage || !configLocalStorage.ultimaModificacion || configLocalStorage.ultimaModificacion < userLogged.data.ultimaModificacion)) {
          this.saveConfiguracion(userLogged.data, uploadConfigInGoogle);
          this.toastr.success('Informacin syncronizada correctamente desde google drive', 'Business Control!');
          setTimeout(() => {
            window.location.reload();
          }, 1500);
        }
      }
      //si no hay data en google entonces subimos la que esta en localstorage
      if (userLogged && !userLogged.data) {
        this.uploadConfigWithGoogleDrive();
      }
    }
  }

  private _getConfigFromLocalStorage() {
    const data = this._readConfig();
    return data ? JSON.parse(data) as Configuracion : null;
  }

  private _readConfig() {
    return localStorage.getItem(this.storageKey);
  }

  async saveConfiguracion(config: Configuracion, syncWithGoogle: boolean = true): Promise<Configuracion> {
    config.bienes.forEach(bien => {
      bien.contratoActivo = null;
      bien.comprobantes.forEach(comprobante => {
        comprobante.contratante = null;
        comprobante.bien = null;
      });
    });
    config.ultimaModificacion = new Date();
    localStorage.setItem(this.storageKey, JSON.stringify(config));
    const isLogged = await this.googleService.isUserLogged()
    if (isLogged && syncWithGoogle) {
      this.uploadConfigWithGoogleDrive();
    }
    return config;
  }

  setContratante(contratante: Contratante): void {
    contratante.firmaDigital = this._generateRandomString(25);
    const config: Configuracion = {
      bienes: [],
      contratante: contratante,
      conceptos: [],
      mediosPago: ['Efectivo', 'Transferencia', 'Cheque', 'Otro']
    };
    this.saveConfiguracion(config);
  }

  private _generateRandomString(length: number): string {
    return Math.random().toString(36).substring(2, 2 + length);
  }

  /**
   * Este metodo se utiliza debido a que desde el front los input date arrojan un string como valor y en tiempo de ejecucion este string en formato "2024-07-18" toca convertirlo a date
   * @param dateString fecha en formato string ("2024-07-18")
   * @returns fecha
   */
  instanceDateFromString(dateString: string): Date {
    // Extraer ao, mes y da del string
    const [year, month, day] = dateString.split('-').map(Number);

    // Crear la fecha manualmente usando el constructor de Date con los componentes individuales
    // El mes en el constructor Date es 0-indexed, por eso restamos 1
    return new Date(year, month - 1, day);
  }


  async addComprobante(bienId: string, comprobante: Comprobante): Promise<void> {
    const config = this.getConfiguracion();
    if (config) {
      const bien = config.bienes.find(v => v.id === bienId);
      if (bien) {
        if (!comprobante.id) { comprobante.id = new Date().getTime().toString(); }
        comprobante.contratoId = bien.contratoActivo.id;
        comprobante.bien = bien;
        //Aqu la seteamos manualmente para que en el encriptado quede la url corta y el QR se vea igual
        comprobante.urlCorta = `https://${this._getDomainForShortUrl()}/${this._getAliasForShortUrl(comprobante)}`;
        let startDate = new Date(comprobante.fechaGeneracion.getFullYear(), comprobante.fechaGeneracion.getMonth(), bien.contratoActivo.inicio.getDate());
        let endDate = new Date(comprobante.fechaGeneracion.getFullYear(), comprobante.fechaGeneracion.getMonth(), bien.contratoActivo.inicio.getDate());
        endDate.setMonth(endDate.getMonth() + 1);
        comprobante.periodo = {
          inicio: startDate,
          fin: endDate
        };
        bien.comprobantes.push(comprobante);
        this.saveConfiguracion(config);
        this._updateShortUrl(bienId, comprobante.id);
      }
    }
  }
  private async _updateShortUrl(bienId: string, comprobanteId: string) {
    let tempConfig = this.getConfiguracion();
    if (tempConfig) {
      const bienTemp = tempConfig.bienes.find(v => v.id === bienId);
      const comprobanteTemp = bienTemp.comprobantes.find(t => t.id = comprobanteId);
      //Aqu se llama directamente al servicio de shorturl para crear la url y en caso tal que funcione correctamente deberian coincidir los valores, de haber un error quedaria en el json en blanco y no abria problema ya que si no hay una url corta el sistema seguira funcionando normal
      const urlCorta = await this.shortUrl(this.generateUrlFirmada(comprobanteTemp), comprobanteTemp);
      //Es necesario volver a consultar la configuracion ya que el metodo "generateUrlFirmada" elimina la firma del contratante
      //TODO: tiene oportunidad de mejora este codigo pero aja, son las 12:51 AM y tengo sueo
      let Config = this.getConfiguracion();
      if (Config) {
        const bien = tempConfig.bienes.find(v => v.id === bienId);
        const comprobante = bienTemp.comprobantes.find(t => t.id = comprobanteId);
        comprobante.urlCorta = urlCorta;
        this.saveConfiguracion(Config);
      }
    }
  }

  addServicioPublico(bienId: string, servicio: ReciboServicio): void {
    const config = this.getConfiguracion();
    if (config) {
      const bien = config.bienes.find(v => v.id === bienId);
      if (bien) {
        servicio.fechaGeneracion = this.instanceDateFromString(servicio.fechaGeneracion.toString());
        servicio.fechaPago = this.instanceDateFromString(servicio.fechaPago.toString());
        bien.serviciosPublicos.push(servicio);
        this.saveConfiguracion(config);
      }
    }
  }

  addBien(bien: Bien): void {
    const config = this.getConfiguracion();
    if (config) {
      bien.id = bien.tipo + this.generateBienId(bien.direccion);
      config.bienes.push(bien);
      this.saveConfiguracion(config);
    }
  }


  addContrato(bienId: string, contrato: Contrato): void {
    const config = this.getConfiguracion();
    if (config) {
      const bien = config.bienes.find(v => v.id === bienId);
      if (bien) {
        this._setContratoActivo(bien, contrato);
        contrato.inicio = this.instanceDateFromString(contrato.inicio.toString());
        contrato.id = contrato.tipo.substring(0, 3) + contrato.identificacion.toString() + new Date().getFullYear() + new Date().getMonth() + new Date().getDay() + new Date().getMinutes();
        const contratoId: ContratoID = {
          id: contrato.id,
          fecha: contrato.inicio.toLocaleDateString(),
          tipo: contrato.tipo,
          firmaDigital: config.contratante.firmaDigital
        }
        contrato.id = this.cryptService.encrypt(JSON.stringify(contratoId));
        bien.contratos.push(contrato);
        this.saveConfiguracion(config);
      }
    }
  }

  public async setContratoActivo(bienId: string, contratoId: string) {
    const config = this.getConfiguracion();
    if (config) {
      const bien = config.bienes.find(v => v.id === bienId);
      if (bien) {
        const contrato = bien.contratos.find(c => c.id == contratoId);
        if (contrato) {
          this._setContratoActivo(bien, contrato);
          await this.saveConfiguracion(config);
        }
      }
    }
  }

  private _setContratoActivo(bien: Bien, contrato: Contrato) {
    bien.contratos.forEach(t => {
      t.activo = false;
    });
    contrato.activo = true;
  }

  getBienById(bienId: string): Bien | null {
    const config = this.getConfiguracion();
    if (config) {
      return config.bienes.find(v => v.id === bienId);
    }
    return null;
  }

  private generateBienId(direccion: string): string {
    return direccion.replace(/\s+/g, '').toLowerCase();
  }

  getTotalIngresosPorBien(bienId: string): number {
    let total = 0;
    const config = this.getConfiguracion();
    if (config) {
      const bien = config.bienes.find(v => v.id === bienId);
      if (bien) {
        bien.comprobantes.forEach(t => {
          total += t.total;
        });
      }
    }
    return total;
  }

  private cryptComprobante(comprobante: Comprobante): string {
    // Clonar el objeto usando el operador de propagacin
    const comprobanteClon = { ...comprobante };

    if (comprobanteClon.contratante) comprobanteClon.contratante = { ...comprobante.contratante, firmaDigital: null };
    if (comprobanteClon.bien) comprobanteClon.bien = { ...comprobante.bien, comprobantes: [] };

    return this._cryptObject(comprobanteClon);
  }

  private _cryptObject(object: any) {
    const cache = new Set();
    const jsonString = JSON.stringify(object, (key, value) => {
      if (typeof value === 'object' && value !== null) {
        if (cache.has(value)) {
          // Si ya hemos visitado este objeto, devolver undefined para omitirlo
          return;
        }
        // Aadir el objeto al conjunto
        cache.add(value);
      }
      return value;
    });
    return this.cryptService.encrypt(jsonString);
  }

  generateUrlFirmada(comprobante: Comprobante, path: string = 'business-control-verify'): string {
    return `${window.location.origin}/#/${path}?comprobante=${encodeURIComponent(this.cryptComprobante(comprobante))}`;
  }

  decryptComprobante(comprobanteString: string): Comprobante {
    const temp = this.cryptService.decrypt(comprobanteString);
    return JSON.parse(temp) as Comprobante;
  }

  downloadConfig(): void {
    const config = this._getConfigFromLocalStorage();
    if (config) {
      // Crea un blob con el contenido JSON
      const blob = new Blob([JSON.stringify(config, null, 2)], { type: 'application/json' });

      // Crea un enlace para la descarga
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = config.contratante.identificacion + '-' + config.contratante.firmaDigital + '.json';

      // Simula un clic en el enlace para iniciar la descarga
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  }

  private uploadConfigWithGoogleDrive(): void {
    const config = this._getConfigFromLocalStorage();
    if (config) {
      // Crea un blob con el contenido JSON
      const blob = new Blob([JSON.stringify(config, null, 2)], { type: 'application/json' });
      const fileName = `${config.contratante.identificacion}-${config.contratante.firmaDigital}.json`;

      // Llama al mtodo para subir el archivo a Google Drive
      this.googleService.uploadFileWithFolderDetection(blob, fileName);
    }
  }

  deleteData(): void {
    localStorage.removeItem(this.storageKey);
  }

  // Define el formato internacional deseado
  formatoInternacional(date: Date): string {

    // Si date es una cadena, convirtelo a un objeto Date
    date = typeof date === 'string' ? new Date(date) : date;
    // Define el formato internacional deseado
    const _formatoInternacional = new Intl.DateTimeFormat('es-CO', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
    return _formatoInternacional.format(date);
  }

  /**
   * Documentacion: https://tinyurl.com/app/dev
   * @param url url larga
   * @returns url corta
   */
  async shortUrl(url: string, comprobante: Comprobante): Promise<string> {
    try {
      const body: RequestTinyUrl = {
        alias: this._getAliasForShortUrl(comprobante),
        description: `CP del periodo ${this.formatoInternacional(comprobante.periodo.inicio)} al ${this.formatoInternacional(comprobante.periodo.fin)}`,
        domain: this._getDomainForShortUrl(),
        url: url
      }
      const response = await this._httpService.post<ResponseTinyUrl>('https://api.tinyurl.com/create', body, {
        headers: {
          'Authorization': 'Bearer Vj3AWgQXdbqfUWmkCiGAYSlfvyHygfDpk7lYbxeu0ptFlMvETd0kgMzmsO8z'
        }
      }).toPromise();
      if (response.code == 0) {
        return response.data.tiny_url
      }
      return null;
    } catch (error) {
      this.toastr.error("Error generando url corta: " + JSON.stringify(error), 'Business Control!');
      return null;
    }
  }

  private _getDomainForShortUrl(): string {
    return 'tinyurl.com';
  }

  private _getAliasForShortUrl(comprobante: Comprobante): string {
    return `C${comprobante.bien.contratoActivo.identificacion}CP${comprobante.id}`;
  }
}

interface RequestTinyUrl {
  url: string
  domain: string
  alias: string
  description: string
}


interface ResponseTinyUrl {
  data: Data
  code: number
  errors: any[]
}

interface Data {
  domain: string
  alias: string
  deleted: boolean
  archived: boolean
  tags: string[]
  analytics: Analytic[]
  tiny_url: string
  created_at: string
  expires_at: any
  url: string
}

interface Analytic {
  enabled: boolean
  public: boolean
}
