import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Summary } from 'src/app/entities/summary';
import { SummaryResumeService } from 'src/app/services/summary-resume/summary-resume.service';
import { Router } from '@angular/router';
import { Contratante, Contrato, Configuracion, Bien, Concepto, Comprobante } from 'src/app/entities/business-control';
import { BusinessControlService } from 'src/app/services/business-control/business-control.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { GoogleService, UserGoogle } from 'src/app/services/google-services/google.service';
import { toJSDate } from '@ng-bootstrap/ng-bootstrap/datepicker/ngb-calendar';
import { Amortizacion, CreditService, ResponseAmortizacion } from 'src/app/services/credit/credit.service';
@Component({
  selector: 'app-business-control-settings',
  templateUrl: './business-control-settings.component.html',
  styleUrls: ['./business-control-settings.component.css']
})
export class BusinessControlSettingsComponent implements OnInit {
  configuracionForm: FormGroup;
  config: Configuracion | null = null;
  bienform: FormGroup;
  contratoForm: FormGroup;
  isConfigured = false;
  bienes: Bien[] = [];
  selectedBien: Bien | null = null;
  selectedBienId: string;
  registrarNuevaBien = false;
  registrarNuevoContrato = false;
  tipoBien: 'Arrendamiento' | 'Credito' = 'Arrendamiento';
  @ViewChild('fileInput') fileInput: ElementRef<HTMLInputElement>;
  userLogger: UserGoogle;
  responseAmortizacion: ResponseAmortizacion;

  constructor(private creditService: CreditService, private toastr: ToastrService, private googleService: GoogleService, private fb: FormBuilder, private bussinessServices: BusinessControlService) {
    this.configuracionForm = this.fb.group({
      contratanteCedula: ['', Validators.required],
      contratanteNombre: ['', Validators.required],
      contratanteTelefono: ['', Validators.required],
      contratanteDireccion: ['', Validators.required],
      contratanteEmail: ['', Validators.required],
      contratanteMostrarDatosPersonales: [false],
      submitted: [false]
    });
    this.bienform = this.fb.group({
      direccion: ['', Validators.required],
      barrio: ['', Validators.required],
      ciudad: ['', Validators.required],
      submitted: [false]
    });
    this.contratoForm = this.fb.group({
      contratoNombre: ['', Validators.required],
      contratoCedula: ['', Validators.required],
      contratoTelefono: ['', Validators.required],
      contratoMensualidad: [0],
      contratoValorDeposito: [0],
      contratoTasaEM: [0],
      contratoValorCredito: [0],
      contratoPlazoMeses: [0],
      contratoTarifaFija: [0],
      contratoFechaInicio: ['', Validators.required],
      contratoObservacion: [''],
      submitted: [false]
    });
  }

  ngOnInit(): void {
    this.initialiceComponent();
    setTimeout(() => {
      this.getUserLogget();
    }, 300);
    this.getUserLogget();
  }

  private initialiceComponent() {
    this.isConfigured = false;
    this.selectedBien = null;
    this.responseAmortizacion = null;
    this.config = this.bussinessServices.getConfiguracion();
    if (this.config) {
      this.isConfigured = true;
      this.bienes = this.config.bienes;
      const bienId = this.bussinessServices.getSelectedId() ?? this.bienes[0]?.id;
      this.tipoBien = 'Arrendamiento';
      this.onBienSelect(bienId);
      if (!this.selectedBien || !this.selectedBien.contratoActivo) {
        this.registrarNuevoContrato = true;
        return;
      }
    }
    this.configuracionForm.reset();
    this.bienform.reset();
    this.contratoForm.reset();
  }

  Login(): void {
    this.googleService.signIn().then(config => {
      this.userLogger = config?.user;
      if (config.data) {
        this.ngOnInit();
      }
    })
  }

  Logout(): void {
    this.googleService.signOut();
    this.userLogger = null;
  }


  getUserLogget(): void {
    this.googleService.getUser().then(async config => {
      this.userLogger = config?.user;
    })
  }

  onSubmitInitial(): void {
    this.configuracionForm.patchValue({ submitted: true });
    if (this.configuracionForm.valid) {
      const formValues = this.configuracionForm.value;

      const newContratante: Contratante = {
        nombre: formValues.contratanteNombre,
        identificacion: formValues.contratanteCedula,
        telefono: formValues.contratanteTelefono,
        direccion: formValues.contratanteDireccion,
        email: formValues.contratanteEmail,
        mostrarDatosPersonales: formValues.contratanteMostrarDatosPersonales,
        firmaDigital: ''
      };

      if (!this.isConfigured) {
        this.isConfigured = true;
        this.registrarNuevaBien = true;
        this.bussinessServices.setContratante(newContratante);
        this.ngOnInit();
      } else {
        this.toastr.info("Ya configuraste el sistema!", "Business Control");
      }
    }
  }

  onSubmitBien(): void {
    this.bienform.patchValue({ submitted: true });
    if (this.bienform.valid) {
      const formValues = this.bienform.value;
      const newBien: Bien = {
        id: '',
        direccion: formValues.direccion,
        barrio: formValues.barrio,
        ciudad: formValues.ciudad,
        tipo: this.tipoBien,
        contratos: [],
        comprobantes: [],
        serviciosPublicos: []
      };
      this.registrarNuevaBien = false;
      this.registrarNuevoContrato = true;
      this.bussinessServices.addBien(newBien);
      this.ngOnInit();
    }
  }

  onSubmitContrato(): void {
    this.contratoForm.patchValue({ submitted: true });
    if (this.contratoForm.valid) {
      const formValues = this.contratoForm.value;

      const newContrato: Contrato = {
        tipoDocumento: 'CC',
        id: formValues.contratoCedula,
        nombre: formValues.contratoNombre,
        identificacion: formValues.contratoCedula,
        telefono: formValues.contratoTelefono,
        observacion: formValues.contratoObservacion,
        mensualidad: formValues.contratoMensualidad,
        valorDeposito: formValues.contratoValorDeposito,
        inicio: formValues.contratoFechaInicio,
        tipo: this.selectedBien.tipo,
        activo: true,
        plazoMeses: formValues.contratoPlazoMeses,
        tasaEM: formValues.contratoTasaEM,
        valorCredito: formValues.contratoValorCredito,
        tarifaFija: formValues.contratoTarifaFija,
      };
      this.config.conceptos = this.getConceptos(newContrato);
      this.bussinessServices.saveConfiguracion(this.config);
      this.bussinessServices.addContrato(this.selectedBien.id, newContrato);
      this.ngOnInit();
    }
  }

  private getConceptos(contrato: Contrato): Concepto[] {
    const conceptoCreditos: Concepto[] = [
      {
        descripcion: BusinessControlService.CONCEPTO_INTERESES,
        valor: 0,
        tipo: BusinessControlService.TIPO_BIEN_CREDITO
      },
      {
        descripcion: BusinessControlService.CONCEPTO_CAPITAL,
        valor: 0,
        tipo: BusinessControlService.TIPO_BIEN_CREDITO
      },
      {
        descripcion: BusinessControlService.CONCEPTO_TARIFA_FIJA,
        valor: contrato.valorDeposito,
        tipo: BusinessControlService.TIPO_BIEN_CREDITO
      },
      {
        descripcion: BusinessControlService.CONCEPTO_ABONO_CAPITAL,
        valor: 0,
        tipo: BusinessControlService.TIPO_BIEN_CREDITO
      },
      {
        descripcion: BusinessControlService.CONCEPTO_OTRO,
        valor: 0,
        tipo: BusinessControlService.TIPO_BIEN_CREDITO
      },
    ];

    const conceptosArrendamiento: Concepto[] = [
      {
        descripcion: BusinessControlService.CONCEPTO_MENSUALIDAD,
        valor: contrato.mensualidad,
        tipo: BusinessControlService.TIPO_BIEN_ARRIENDO
      },
      {
        descripcion: BusinessControlService.CONCEPTO_DEPOSITO,
        valor: contrato.valorDeposito,
        tipo: BusinessControlService.TIPO_BIEN_ARRIENDO
      },
      {
        descripcion: BusinessControlService.CONCEPTO_DEPOSITO_50,
        valor: Math.round(contrato.valorDeposito / 2),
        tipo: BusinessControlService.TIPO_BIEN_ARRIENDO
      },
      {
        descripcion: BusinessControlService.CONCEPTO_OTRO,
        valor: 0,
        tipo: BusinessControlService.TIPO_BIEN_ARRIENDO
      },
    ];
    if (this.config.conceptos.length > 0) {
      return [...conceptoCreditos, ...conceptosArrendamiento]
    }
    if (contrato.tipo == BusinessControlService.TIPO_BIEN_ARRIENDO) {
      return conceptosArrendamiento;
    }
    return conceptoCreditos;
  }

  onBienSelect(bienId: string): void {
    this.selectedBienId = bienId;
    this.bussinessServices.setSelectedId(bienId);
    this.selectedBien = this.bussinessServices.getBienById(bienId);
    this.tipoBien = this.selectedBien.tipo;
    if (this.selectedBien && this.selectedBien.contratoActivo && this.selectedBien.contratoActivo.tipo == 'Credito') {
      this.responseAmortizacion = this.creditService.calcularAmortizacion(this.selectedBien);
    }
  }

  redireccionarAlComprobante(cuota: Amortizacion): void {
    const url = this.bussinessServices.generateUrlFirmada(cuota.comprobante);
    window.open(url, '_blank');
  }

  redireccionarGeneracionComprobante(cuota: Amortizacion): void {
    const comprobante: Comprobante = {
      id: cuota?.fechaCuota?.toLocaleDateString('en-CA').replace(/-/g, '') ?? cuota.index.toString(),
      conceptos: [
        {
          valor: cuota.interes,
          descripcion: BusinessControlService.CONCEPTO_INTERESES
        },
        {
          valor: cuota.abonoCapital,
          descripcion: BusinessControlService.CONCEPTO_CAPITAL
        },
        {
          valor: 0,
          descripcion: BusinessControlService.CONCEPTO_ABONO_CAPITAL
        }
      ],
      total: 0,
      fechaGeneracion: cuota.fechaCuota,
      periodo: {
        inicio: cuota.fechaCuota,
        fin: cuota.fechaCuota,
      },
      observacion: `Cuota ${cuota.index}`,
      contratoId: this.selectedBien.contratoActivo.id,
      medioPago: 'Efectivo',
    };
    if (cuota.tarifaFija > 0) {
      comprobante.conceptos.push({
        valor: cuota.tarifaFija,
        descripcion: BusinessControlService.CONCEPTO_TARIFA_FIJA
      });
    }
    window.location.href = this.bussinessServices.generateUrlFirmada(comprobante, "business-control");
  }

  async setContratoActivo(contrato: Contrato) {
    await this.bussinessServices.setContratoActivo(this.selectedBienId, contrato.id);
    this.ngOnInit();
  }

  downloadConfig(): void {
    this.bussinessServices.downloadConfig();
  }

  isProcessingFile = false;

  // Mtodo para manejar el cambio de archivo
  onFileChange(event: Event) {
    if (this.isProcessingFile) {
      return;  // Si ya se est procesando un archivo, salir
    }
    this.isProcessingFile = true;

    const input = event.target as HTMLInputElement;
    if (input.files && input.files.length) {
      const file = input.files[0];
      this.readAndValidateFile(file);
      input.value = ''; // Restablecer el valor del input
    } else {
      this.isProcessingFile = false;
    }
  }


  // Mtodo para leer el archivo y validarlo
  readAndValidateFile(file: File) {
    if (file.size === 0) {
      this.toastr.info("Archivo vacio", "Business Control");
      this.isProcessingFile = false;
      return;
    }

    const reader = new FileReader();
    reader.onload = async (e: ProgressEvent<FileReader>) => {
      try {
        this.isProcessingFile = false;
        const json = JSON.parse(reader.result as string);
        if (json) {
          this.config = await this.bussinessServices.saveConfiguracion(json);
          this.toastr.success("Datos importados correctamente", "Business Control");
          this.ngOnInit();
        }
      } catch (error) {
        this.toastr.error('Error al procesar el archivo JSON - ' + JSON.stringify(error) + ' | ' + JSON.stringify(reader.result), "Business Control");
        alert();
      } finally {
        reader.onload = null;
      }
    };
    reader.readAsText(file);
  }

  deleteData(): void {
    const result = confirm('Ests seguro de que deseas continuar?');
    if (result) {
      this.bussinessServices.deleteData();
      this.ngOnInit();
    } else {
      this.toastr.info("Operacin de eliminacin cancelada", "Business Control");
    }
  }

}
