import { Injectable } from '@angular/core';
import html2pdf from 'html2pdf.js';

@Injectable({
  providedIn: 'root'
})
export class PdfService {

  constructor() { }
  private options = {
    filename: '',
    image: { type: 'png', quality: 0.98 },
    html2canvas: { scale: 4 },  // Ajusta la escala para mejorar la resolucin
    jsPDF: { unit: 'mm', format: 'a4', orientation: 'portrait', compress: true },
    margin: 10 // margen de 10mm alrededor
  };
  public async generatePDF(namefile: string, content: HTMLElement) {
    this.options.filename = namefile + '.pdf';

    await html2pdf().from(content).set(this.options).save();
  }

  public getPdfInBlob(namefile: string, content: HTMLElement): Promise<Blob> {
    this.options.filename = namefile + '.pdf';
    return html2pdf().from(content).set(this.options).outputPdf('blob');
  }
}
