import { Component, OnDestroy, OnInit } from '@angular/core';
import { GoogleService, TransactionFromGoogle, UserGoogle } from 'src/app/services/google-services/google.service';
import { BaseComponent } from '../common/base-component';
import { AccountControlManagementCategoriesComponent } from './account-control-management-categories/account-control-management-categories.component';
import { AccountControlManagementAccountComponent } from './account-control-management-account/account-control-management-account.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AccountControlService } from 'src/app/services/account-control/account-control.service';
import { Account, SettingsData } from 'src/app/entities/account-control';
import { AccountControlManagementTransactionComponent } from './account-control-management-transaction/account-control-management-transaction.component';

@Component({
  selector: 'app-account-control',
  templateUrl: './account-control.component.html',
  styleUrls: ['./account-control.component.css']
})
export class AccountControlComponent extends BaseComponent implements OnInit, OnDestroy {


  public transactions: TransactionFromGoogle[] = [];
  public accounts: Account[] = [];

  public settionsData: SettingsData;
  public showAllAccounts: boolean = false;
  public heritage: number = 0;

  constructor(googleService: GoogleService, private modalService: NgbModal, private accountService: AccountControlService) {
    super(googleService)
  }


  async initialiceComponent(): Promise<void> {
    console.log('Inicializando componente...');
    this.settionsData = this.accountService.getTransactions(this.settionsData);
    if (this.isLogged()) {
      this.settionsData = await this.accountService.syncTransactions(this.configFromGoogle);
    }
    console.log('Componente inicializado...', this.settionsData);
    this.transactions = this.settionsData.transactions;
    this.filterAccounts();
  }

  ngOnInit(): void {
    this.init();
  }

  ngOnDestroy(): void {
    this.detroy();
  }

  openEditAccount(account: Account) {
    const componentRef = this.modalService.open(AccountControlManagementAccountComponent, { size: 'xl' });
    const componentInstance = componentRef.componentInstance as AccountControlManagementAccountComponent;
    componentInstance.accountToEdit = account;
    componentInstance.onFinish.subscribe((data) => {
      this.initialiceComponent();
      componentRef.close();
    });
  }

  openCreateAccount(transaction?: TransactionFromGoogle) {
    const componentRef = this.modalService.open(AccountControlManagementAccountComponent, { size: 'xl' });
    const componentInstance = componentRef.componentInstance as AccountControlManagementAccountComponent;
    componentInstance.transaction = transaction;
    componentInstance.onFinish.subscribe((data) => {
      this.initialiceComponent();
      componentRef.close();
    });
  }

  openManagementCategories(transaction?: TransactionFromGoogle) {
    const componentRef = this.modalService.open(AccountControlManagementCategoriesComponent, { size: 'xxl' });
    const componentInstance = componentRef.componentInstance as AccountControlManagementCategoriesComponent;
    componentInstance.transaction = transaction;
    componentInstance.settionsData = this.settionsData;
    componentInstance.onFinish.subscribe((data) => {
      this.initialiceComponent();
      componentRef.close();
    });
  }

  openCreateTransaction(transaction?: TransactionFromGoogle) {
    const componentRef = this.modalService.open(AccountControlManagementTransactionComponent, { size: 'xl' });
    const componentInstance = componentRef.componentInstance as AccountControlManagementTransactionComponent;
    componentInstance.transaction = transaction;
    componentInstance.settingsData = this.settionsData;
    componentInstance.onTransactionSave.subscribe((data) => {
      this.settionsData = null;
      this.transactions = [];
      this.initialiceComponent();
      componentRef.close();
    });
  }

  public filterAccounts(): void {
    if (this.showAllAccounts) {
      this.accounts = this.settionsData.accounts;
    } else {
      this.accounts = this.settionsData.accounts.filter(a => a.isPrincipal);
    }
    this.heritage = this.accounts.reduce((sum, acc) => sum + acc.balance, 0);
  }

  public getPercentageChange(account: Account): number {
    if (account.lastBalance === 0) {
      // Evita la divisin por cero, devuelve 0 o alguna otra indicacin
      return 0;
    }
    const change = ((account.balance - account.lastBalance) / account.lastBalance) * 100;
    return change;
  }

  public deleteData(): void {
    this.accountService.deleteData();
    window.location.reload();
  }

  async syncGoogleDrive(): Promise<void> {
    await this.accountService.syncWithGoogleDrive(this.settionsData, true);
  }
}
