import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AccountConstant, Category, SettingsData, Transaction } from 'src/app/entities/account-control';
import { AccountControlService } from 'src/app/services/account-control/account-control.service';

@Component({
  selector: 'app-account-control-management-transaction',
  templateUrl: './account-control-management-transaction.component.html',
  styleUrls: ['./account-control-management-transaction.component.css']
})
export class AccountControlManagementTransactionComponent implements OnInit {
  transactionForm: FormGroup;
  isSubmitted = false;

  @Input() transaction: Transaction;
  @Input() settingsData: SettingsData;
  @Output() onTransactionSave = new EventEmitter<Transaction>();

  public get categories(): Category[] {
    if (this.transactionForm.value) {
      const typeTransaction = this.transactionForm.value.type;
      return this.settingsData.budgetSettings.categories.filter(t => t.type === typeTransaction);
    }
    return this.settingsData?.budgetSettings?.categories ?? [];
  }

  incomeType = AccountConstant.TRANSACTION_TYPE_INCOME;
  expenseType = AccountConstant.TRANSACTION_TYPE_EXPENSE;

  constructor(private fb: FormBuilder, private accountService: AccountControlService) {
    this.transactionForm = this.fb.group({
      originalAccount: ['', Validators.required],
      amount: [0, [Validators.required, Validators.min(0.01)]],
      date: [this.convertToColDate(new Date()).toISOString().substring(0, 10), Validators.required],
      description: [''],
      categoryId: [''],//no es requerido porque las transacciones no deberian tener categorias o bueno es a decision del usuario
      type: [AccountConstant.TRANSACTION_TYPE_EXPENSE, Validators.required],
      targetAccountId: [''],
      isProcessed: [false],
      originalBody: [''],
      canProcessed: [true]
    });
  }

  ngOnInit(): void {
    if (this.transaction) {
      this.loadTransactionData();
    }
  }

  loadTransactionData(): void {
    console.log(new Date(this.transaction.date));
    console.log(this.convertToColDate(new Date(this.transaction.date)));
    console.log(this.convertToColDate(new Date(this.transaction.date)).toISOString());
    console.log(this.convertToColDate(new Date(this.transaction.date)).toISOString().substring(0, 10));
    this.transactionForm.patchValue({
      originalAccount: this.transaction.originalAccount,
      amount: this.transaction.type == AccountConstant.TRANSACTION_TYPE_EXPENSE ? -this.transaction.amount : this.transaction.amount,
      date: this.convertToColDate(new Date(this.transaction.date)).toISOString().substring(0, 10),
      description: this.transaction.description,
      categoryId: this.transaction.categoryId,
      type: this.transaction.type,
      targetAccountId: this.transaction.targetAccountId,
      isProcessed: this.transaction.isProcessed,
      originalBody: this.transaction.originalBody,
      canProcessed: this.transaction.canProcessed
    });
  }

  private convertToColDate(date: Date): Date {
    date.setHours(date.getHours() - 5);
    return date;
  }

  private convertToUTCDate(date: Date): Date {
    const newDate = new Date(date);
    newDate.setHours(newDate.getHours() + 5);
    return newDate;
  }


  onSubmit(): void {
    this.isSubmitted = true;
    if (this.transactionForm.valid) {
      const formValues = this.transactionForm.value;
      const transactionToSave: Transaction = {
        id: this.transaction ? this.transaction.id : '',
        ...formValues
      };
      if (this.transaction) {
        this.transaction.date = new Date(this.transaction.date);
      }
      transactionToSave.date = this.convertToUTCDate(new Date(transactionToSave.date));
      if (this.transaction && this.transaction.date && this.isSameDate(transactionToSave.date, this.transaction.date)) {
        transactionToSave.date = this.transaction.date;
      }
      if (transactionToSave.categoryId) {
        const category = this.settingsData.budgetSettings.categories.find(c => c.id == transactionToSave.categoryId);
        if (category && !transactionToSave.description.toLowerCase().includes(category.name.toLowerCase())) {
          if (!transactionToSave.id) {
            transactionToSave.description = transactionToSave.description ? `${transactionToSave.description} = ${category.name}` : category.name;
          } else {
            transactionToSave.description = transactionToSave.description ? `${transactionToSave.description}` : category.name;
          }
        }
      }
      this.accountService.saveTransaction(transactionToSave);
      this.onTransactionSave.emit(transactionToSave);
    }
  }

  private isSameDate(date1: Date, date2: Date): boolean {
    return date1.getFullYear() === date2.getFullYear() &&
      date1.getMonth() === date2.getMonth() &&
      date1.getDate() === date2.getDate();
  }

}
